import React from 'react';
import Text from 'game/Views/TextManager/TextManager.js';
import Dialogue from 'game/Views/Dialogue/Dialogue.js';
import Scene from 'game/Views/Scene/Scene.js';
import Choice from 'views/Choices/v1/Choices.js';
import keys from './keys';
import simulation from './sim.js';
import {getImg, getPar, roundTo} from 'utilities/Format.js';
import {MCQ} from 'utilities/Create.js';
import {get, DELETED} from 'utilities/Merge';
import {getNewEmployeeText, getMoneyText, getKnownEmployeeText, getJobsText, 
        getReviewText,getReviewJobText, getReviewAggregateText} from './texts';
import {solveWorkRate} from './views';
import {dgEmployeeWorkRate,  dgCharacters, dgWorkRateVarRhs, //dgWorkRateReal, 
       getDialogue, getClientDialogue, dgSolveJob, 
       getWorkerDialogue} from './dialogues/index';
import {log, sendError} from 'utilities/Measure';
import { makeChange } from 'utilities/Merge';
import {getText} from './views/index';

var o = {
    //n keys must match k values
    sim: simulation,
    n: {},
    k: keys,
    start: 'intro',
    ['trees']: { //dgWorkRateTheory
        ['intro']: function(read, settings, viewProps){
            let dialogue = getDialogue('dgIntro');
            return {node: 'intro', label: keys.aclocStory,
                view: (write, makeChange) => {
                    return <Dialogue {...getPar(settings, viewProps, makeChange, `dgIntro`)}
                            dialogue={dialogue} 
                            move="dgWorkRateTheory"
                            //move="endgame"
                            write={write}
                            read={read}
                            addons={dialogue.pull(read, write)}
                        />
                }
            }
        },
        ['dgWorkRateTheory']: function(read, settings, viewProps){
            let dialogue = getDialogue('dgWorkRateTheory');
            return {node: 'dgWorkRateTheory', label: keys.aclocStory,
                view: (write, makeChange) => {
                    return <Dialogue {...getPar(settings, viewProps, makeChange, `dgWorkRateTheory`)}
                            dialogue={dialogue} 
                            move="hub"
                            write={write}
                            read={read}
                            addons={dialogue.pull(read, write)}
                        />
                }
            }
        },
        // ['dgSampleSiteSize']: function(read, settings, viewProps){
        //     let dialogue = getDialogue('dgSampleSiteSize');
        //     return {node: 'dgSampleSiteSize', label: keys.aclocStory,
        //         view: (write, makeChange) => {
        //             return <Dialogue {...getPar(settings, viewProps, makeChange, `dgSampleSiteSize`)}
        //                     dialogue={dialogue} 
        //                     move="hub"
        //                     write={write}
        //                     read={read}
        //                     addons={dialogue.pull(read, write)}
        //                 />
        //         }
        //     }
        // },
        ['testDialogue']: function(read, settings, viewProps){
            let dgKey = 'dgWorkRateNeg';
            let mistake = {key: dgKey, eq: -4, employee: {name: 'Whesker', workTotal: 3, 
                            eq: "1/2.222=1/5+1/x", specs: ["dark", "mold"], playerWorkRate: 4}}
            let dialogue = getDialogue(dgKey);
            return {
                node: 'testDialogue', label: keys.aclocData,
                view: (write, makeChange) => {
                    return <Dialogue {...getPar(settings, viewProps, makeChange, dialogue.key)}
                                dialogue={dialogue} 
                                move="hub"
                                write={write}
                                read={read}
                                addons={dialogue.pull(read, write, mistake)}
                            />
                }
            }
        },
        ['hub']: function(read, settings, viewProps){
            return {node: 'hub', label: keys.aclocData,
                view: (write, makeChange) => {
                    return Scene('hub',{
                        settings, viewProps, makeChange,
                        textClassName: 'text-left',
                        audio: [
                            {id:  keys.mainTheme, stop:true},
                            {id: keys.mainTheme, loop: true},
                        ],
                        text: [
                            ...getText('getBusinessState')(read, write),
                            {text: 'todays jobs', tag: 'h3', className: 'text-center'},
                            ...getText('getRequest')(read, write)
                        ],
                        choice: {options: [
                            //dgHeadSister
     
                            <Text text={`Talk to ${keys.sis0}`} out={[
                                {type: 'set', path:'move', value: {location: 'sis0'}},
                            ]} />,
                            <Text text={`Talk to ${keys.sis1}`} out={[
                                {type: 'set', path:'move', value: {location: 'sis1'}},
                            ]} />,
                            <Text text={`Talk to ${keys.sis2}`} out={[
                                {type: 'set', path:'move', value: {location: 'sis2'}},
                            ]} />,
                            // <Text text={"end"} out={[
                            //     {type: 'set', path:'move', value: {location: 'endgame'}},
                            // ]} />,
                            // <Text text={"test loop"} out={[
                            //     {type: 'set', path:'move', value: {location: 'testLoop'}},
                            // ]} />,
                            // <Text text={"check business stats"} out={[
                            //     {type: 'set', path:'move', value: {location: 'business'}},
                            // ]} />
                        ]}
                    })
                }
            }
        },
        ['sis0']: function(read, settings, viewProps){
            let dialogue = getDialogue(`dgHeadSister`);
            return {
                node: 'sis0', label: keys.aclocData,
                view: (write, makeChange) => {
                    return <Dialogue {...getPar(settings, viewProps, makeChange, dialogue.key)}
                                dialogue={dialogue} 
                                move="hub"
                                write={write}
                                read={read}
                                addons={dialogue.pull(read, write)}
                            />
                }
            }
        },
        ['sis1']: function(read, settings, viewProps){
            let dialogue = getDialogue(`dgFastSisterDenominator`);
            return {
                node: 'sis1', label: keys.aclocData,
                view: (write, makeChange) => {
                    return <Dialogue {...getPar(settings, viewProps, makeChange, dialogue.key)}
                                dialogue={dialogue} 
                                move="hub"
                                write={write}
                                read={read}
                                addons={dialogue.pull(read, write)}
                            />
                }
            }
        },
        ['sis2']: function(read, settings, viewProps){
            let dialogue = getDialogue(`dgSister2`);
            return {
                node: 'sis2', label: keys.aclocData,
                view: (write, makeChange) => {
                    return <Dialogue {...getPar(settings, viewProps, makeChange, dialogue.key)}
                                dialogue={dialogue} 
                                move="hub"
                                write={write}
                                read={read}
                                addons={dialogue.pull(read, write)}
                            />
                }
            }
        },
        ['testLoop']: function(read, settings, viewProps){
            return {node: 'testLoop', label: keys.aclocData,
                view: (write, makeChange) => {
                    if(read.sentHash !== read.recievedHash){
                        return Scene('loopWait', {
                            settings, viewProps, makeChange, 
                            text: 'Waiting for results...'
                        })
                    }else{
                        let next = get('meta.move.location', read, write, 'hub');
                        return Scene('testLoop',{
                            settings, viewProps, makeChange,
                            text: [
                                {text: 'test loop', tag: 'h3', className: 'text-center'}
                            ],
                            choice: {options: [
                                <Text text={"go to " + next} out={[
                                    {type: 'set', path:'move', value: {location: next}},
                                ]} />
                            ]}
                        })
                    }
                    
                }
            }
        },
        ['work']:function(read, settings, viewProps){
            return {node: 'work', label: keys.aclocData,
                view: (write, makeChange) => {
                    let fsm = get(`temp.fsm`, read, write, 'problemStory');
                    let requestId = get(`meta.requestId`, read, write, null);
                    let requests = get(`requests`, read, write, null);
                    let request = requests[requestId];
                    let problemDialogue = getDialogue('dgClientStory');
                    return <Dialogue {...getPar(settings, viewProps, makeChange, problemDialogue.key)}
                        dialogue={problemDialogue} 
                        onExit={(id,v) => {
                            return [
                                {type: 'set', path:'move', value: {location: 'combinedTime'}},
                            ]
                        }}
                        write={write}
                        read={read}
                        addons={problemDialogue.pull(read, write, request)}
                    />
                    // if(fsm === 'problemStory'){
                    //     let sceneDialogue = (getDialogue(request.clientId));
                    //     return <Dialogue {...getPar(settings, viewProps, makeChange, request.clientId)}
                    //                     dialogue={sceneDialogue} 
                    //                     onExit={(id,v) => {
                    //                         return [
                    //                             {type: 'set', path:'temp.fsm', value: 'problemSetup'},
                    //                             {type: 'set', path:`flags.${request.clientId}`, value: true},
                    //                         ]
                    //                     }}
                    //                     write={write}
                    //                     read={read}
                    //                     addons={sceneDialogue.pull(read, write, request)}
                    //                 />
                    // }else if(fsm === 'problemSetup'){
                    //     let problemDialogue = getDialogue('problemSetupInventor0');
                    //     return <Dialogue {...getPar(settings, viewProps, makeChange, problemDialogue.key)}
                    //         dialogue={problemDialogue} 
                    //         onExit={(id,v) => {
                    //             return [
                    //                 {type: 'set', path:'move', value: {location: 'combinedTime'}},
                    //             ]
                    //         }}
                    //         write={write}
                    //         read={read}
                    //         addons={problemDialogue.pull(read, write, request)}
                    //     />
                    // }
                }
            }
        },
        ['combinedTime']: function(read, settings, viewProps){
            return {node: 'combinedTime', label: keys.aclocData,
                view: (write, makeChange) => {
                    let requestId = get(`meta.requestId`, read, write, null);
                    let requests = get(`requests`, read, write, null);
                    let request = requests[requestId];
                    let mistake = get('temp.mistake', read, write, false);
                    let flags = get('flags', read, write, false);
                    if(mistake){
                        let dialogue = getDialogue(mistake.key);
                    
                        return <Dialogue {...getPar(settings, viewProps, makeChange, dialogue.key)}
                            dialogue={dialogue} 
                            onExit={(id,v)=>{
                                return [
                                    {type: 'set', path:'temp.mistake', value: DELETED},
                                ]
                            }}
                            write={write}
                            read={read}
                            addons={dialogue.pull(read, write, mistake)}
                        />
                    }
                    else{
                        let dgCombinedWorkRate = getDialogue('dgCombinedWorkRate');
                        return <Dialogue {...getPar(settings, viewProps, makeChange, 'dgCombinedWorkRate')}
                                dialogue={dgCombinedWorkRate} 
                                onExit={(id,v) => {
                                    //get workRate from value, push onto sisterWorkRates
                                    //combinedWorkRate
                                    if(v.temp.mistake){
                                        return [
                                            {type: 'set', path: 'temp.mistake', value: v.temp.mistake}
                                        ]
                                    }else if(v.temp.combinedWorkRate){
                                    
                                        let nRequest = {...request, combinedTime: v.temp.combinedWorkRate.eq};
                                        return [
                                            {type: 'set', path:`requests.${requestId}`, value: nRequest},
                                            {type: 'set', path:`move`, value: {location: 'convertTime'}},
                                        ];
                                    }
                                    return [];
                                }}
                                write={write}
                                read={read}
                                addons={dgCombinedWorkRate.pull(read, write, request)}
                            />
                    }
                }
            }
        },
        ['convertTime']: function(read, settings, viewProps){
            return {node: 'convertTime', label: keys.aclocData,
                view: (write, makeChange) => {
                    let requestId = get(`meta.requestId`, read, write, null);
                    let requests = get(`requests`, read, write, null);
                    let request = requests[requestId];
                    let mistake = get('temp.mistake', read, write, false);
                    let flags = get('flags', read, write, false);
                    if(mistake){
                        let dialogue = getDialogue(mistake.key);
                        return <Dialogue {...getPar(settings, viewProps, makeChange, dialogue.key)}
                            dialogue={dialogue} 
                            onExit={(id,v)=>{
                                return [
                                    {type: 'set', path:'temp.mistake', value: DELETED},
                                ]
                            }}
                            write={write}
                            read={read}
                            addons={dialogue.pull(read, write, mistake)}
                        />
                    }else {
                        let dgUnitConversion = getDialogue('dgUnitConversion');
                        return <Dialogue {...getPar(settings, viewProps, makeChange, 'dgUnitConversion')}
                                dialogue={dgUnitConversion} 
                                onExit={(id,v) => {
                                    if(v.temp.mistake){
                                        return [
                                            {type: 'set', path: 'temp.mistake', value: v.temp.mistake}
                                        ]
                                    }else if(v.temp.solutionHours){
                                        let nRequest = {...request, solutionHours: v.temp.solutionHours};
                                        return [
                                            {type: 'set', path:`requests.${requestId}`, value: nRequest},
                                            {type: 'set', path:`flags.dgUnitConversion`, value: (flags.dgUnitConversion + 1) || 1 },
                                            {type: 'set', path:`move`, value: {location: 'loop'}},
                                        ];
                                    }
                                    return [];
                                }}
                                write={write}
                                read={read}
                                addons={dgUnitConversion.pull(read, write, request)}
                            />
                    }
                }
            }
        },
        ['loop']: function(read, settings, viewProps){
            return {node: 'loop', label: keys.aclocData,
                view: (write, makeChange) => {
                    if(read.sentHash !== read.recievedHash){
                        return Scene('loopWait', {
                            settings, viewProps, makeChange, 
                            text: 'Waiting for results...'
                        })
                    }else{
                        let review = get(`review`, read, write, null);
                        let reviewAggregate = get(`values.reviewAggregate`, read, write, null);
                        return Scene('loop-jobs',{
                            settings, viewProps, makeChange,
                            text: [
                                ...getText('getReviewClientText')(read, write),
                                ...getText('getReviewReportText')(read, write)
                            ],
                            choice: {options: [
                                <Text text={"back to hub"} out={[
                                    {type: 'set', path:'move', value: {location: 'hub'}},
                                ]} />
                            ]}
                        })
                    }
                    
                }
            }
        },
        ['playDialogue']: function(read, settings, viewProps){
            return {
                node: 'playDialogue', label: keys.aclocData,
                view: (write, makeChange) => {
                    let dialogueKey = get('meta.dialogue', read, write, 'dgWin');
                    let dialogue = getDialogue(dialogueKey);
                    return <Dialogue {...getPar(settings, viewProps, makeChange, dialogueKey)}
                                dialogue={dialogue} 
                                move={dialogue.move || 'hub'}
                                write={write}
                                read={read}
                                addons={dialogue.pull(read, write)}
                            />
                }
            }
        },
        ['assignJob']: function(read, settings, viewProps){
            return {node: 'assignJob', label: keys.aclocData,
                view: (write, makeChange) => {
                    let workerScene = get('temp.workerScene', read, write, null);
                    let mistake = get('temp.mistake', read, write, null);
                    let flags = get('flags', read, write, null);
                    let generic = get('temp.generic', read, write, null);
                    let jobId = get('meta.jobId', read, write, null);
                    let clients = get('clients', read, write, {});
                    let job = clients[jobId];
                    //play cutscene, show dialogue to solve problem
                    let clientId = job.client;
                    //set flag on completion
                    if(mistake){
                        let dialogue = getDialogue(mistake.key);
                        return <Dialogue {...getPar(settings, viewProps, makeChange, dialogue.key)}
                            dialogue={dialogue} 
                            onExit={(id,v)=>{
                                // makeChange(dialogue.key, [
                                //     ...a,
                                //     {type: 'set', path:'temp.mistake', value: DELETED},
                                // ])
                                return [
                                    {type: 'set', path:'temp.mistake', value: DELETED},
                                ]
                            }}
                            write={write}
                            read={read}
                            addons={dialogue.pull(read, write, mistake)}
                        />
                    }else if(workerScene){
                        return <Dialogue {...getPar(settings, viewProps, makeChange, workerScene.key)}
                            dialogue={workerScene} 
                            onExit={(id,v)=>{
                                // makeChange(`node-intro`, [
                                //     {type: 'set', path: 'move', value: {location: 'loop'}},
                                //     {type: 'set', path: 'flags.' + workerScene.key, value: true}
                                // ])
                                return [
                                    {type: 'set', path: 'move', value: {location: 'loop'}},
                                    {type: 'set', path: 'flags.' + workerScene.key, value: true}
                                ]
                            }}
                            write={write}
                            read={read}
                            addons={workerScene.pull(read, write)}
                        />
                    }else if(generic || flags[clientId]){
                        return <Dialogue {...getPar(settings, viewProps, makeChange, `dgSolveJob`)}
                            dialogue={dgSolveJob} 
                            onExit={(id,v)=>{
                                let actions;
                                if(v.temp.mistake){
                                    // makeChange(id, [
                                    //     {type: 'set', path: 'temp.mistake', value: v.temp.mistake}
                                    // ])
                                    actions = [
                                        {type: 'set', path: 'temp.mistake', value: v.temp.mistake}
                                    ]
                                }else if(v.temp.totalTime){
                                    //is the solution
                                    let solution = v.temp.totalTime.eq;
                                    //check if there's a scene for this assigned workers
                                    let names = Object.keys(v.temp.assigned).filter(f => v.temp.assigned[f]);
                                    let workerScene = getWorkerDialogue(names, flags);
                                    actions = [
                                        {type: 'set', path: `clients.${jobId}`, 
                                        value: {...job, workEndReal: v.temp.totalTime.eq, 
                                            workEnd: roundTo(v.temp.totalTime.eq, 2), 
                                            assigned: v.temp.assigned}},
                                        workerScene?{type: 'set', path: 'temp.workerScene', value: workerScene}
                                            :{type: 'set', path: 'move', value: {location: 'loop'}}
                                    ]
                                }else{
                                    // makeChange(id, [
                                    //     {type: 'set', path: 'move', value: {location: 'hub'}}
                                    // ])
                                    actions = [
                                        {type: 'set', path: 'move', value: {location: 'hub'}}
                                    ]
                                }
                                return actions;
                            }}
                            write={write}
                            read={read}
                            addons={dgSolveJob.pull(read, write)}
                        />
                    }else{
                        let clientDialogue = getClientDialogue(clientId);
                        return <Dialogue {...getPar(settings, viewProps, makeChange, `dgAssignJob`)}
                            dialogue={clientDialogue} 
                            onExit={(id,v)=>{
                                if(clientId){
                                    // makeChange(`node-intro`, [
                                    //     {type: 'set', path:'flags.'+clientId, value: true},
                                    // ])
                                    return [
                                        {type: 'set', path:'flags.'+clientId, value: true},
                                    ]
                                }else{
                                    //is generic
                                    // makeChange(`node-intro`, [
                                    //     {type: 'set', path: 'temp.generic', value: true},
                                    // ])
                                    return [
                                        {type: 'set', path: 'temp.generic', value: true},
                                    ]
                                }
                            }}
                            write={write}
                            read={read}
                            addons={clientDialogue.pull(read, write)}
                        />
                    }
                    
                }
            }
        },
        ['employees']:function(read, settings, viewProps){
            return {node: 'employees', label: keys.aclocData,
                view: (write, makeChange) => {
                    let dialogue = getDialogue('dgEmployeeSection');
                    return  <Dialogue {...getPar(settings, viewProps, makeChange, `dgEmployeeSection`)}
                                dialogue={dialogue} 
                                //move='hub'
                                onExit={(id, val) => {
                                   if(val.temp.retry){
                                    return [
                                        {type: 'set', path: 'meta.newEmployee', value: val.temp.retry},
                                        {type: 'set', path:'move', value: {location: 'newEmployee'}}
                                    ]
                                   }else{
                                    return [
                                        {type: 'set', path: 'move', value: {location: 'hub'}}
                                    ]
                                   }
                                }}
                                write={write}
                                read={read}
                                addons={dialogue.pull(read, write)}
                            />
                }
            }
        },
        ['newEmployee']: function(read, settings, viewProps){
            return {node: 'newEmployee', label: keys.aclocData,
                view: (write, makeChange) => {
                    let mistake = get('temp.mistake', read, write, null, true);
                    if(mistake){
                        let dialogue = getDialogue(mistake.key);
                        return <Dialogue {...getPar(settings, viewProps, makeChange, dialogue.key)}
                            dialogue={dialogue} 
                            onExit={(id,v)=>{
                                // makeChange(dialogue.key, [
                                //     {type: 'set', path:'temp.mistake', value: DELETED},
                                // ])
                                return [
                                    {type: 'set', path:'temp.mistake', value: DELETED},
                                ]
                            }}
                            write={write}
                            read={read}
                            addons={dialogue.pull(read, write, mistake)}
                        />
                    }else{
                        return  <Dialogue {...getPar(settings, viewProps, makeChange, `dg-newEmployee`)}
                        dialogue={dgEmployeeWorkRate} 
                      //  move='employees'
                        write={write}
                        read={read}
                        onExit={(id, v) => {
                            if(v.temp.mistake){
                                return [
                                    {type: 'set', path: 'temp.mistake', value: v.temp.mistake}
                                ]
                            }else if(v.temp.solved){
                                let workRate = v.temp.solved;
                                if(isNaN(workRate)) throw Error(`newEmployee onExit error: workRate should be a number`);
                                let employee = {...v.temp.employee};
                                employee.playerWorkRate = workRate;
                                let employees = get('employees', read, write, null);
                                // makeChange(id, [
                                //     {type: 'set', path: 'employees.' + employee.name, value: employee},
                                //     //{type: 'set', path: 'employees', value: employees},
                                //     {type: 'set', path: 'move', value: {location: 'employees'}},
                                // ])
                                return [
                                    {type: 'set', path: 'employees.' + employee.name, value: employee},
                                    {type: 'set', path: 'move', value: {location: 'employees'}},
                                ]
                            }else{
                                // makeChange(id, [
                                //     {type: 'set', path: 'move', value: {location: 'employees'}},
                                // ])
                                return [
                                    {type: 'set', path: 'move', value: {location: 'employees'}},
                                ]
                            }
                            
                        }}
                        addons={dgEmployeeWorkRate.pull(read, write)}
                    />
                    }
                    
                }
            }
        },
        ['endgame']: function(read, settings, viewProps){
            return {node: 'endgame', label: keys.aclocStory,
               // out: () => {return {gameover:true}},
                view: (write, makeChange) => {
                    if(false || read.sentHash !== read.recievedHash){
                        return Scene('endgameWait', {
                            settings, viewProps, makeChange, 
                            text: 'Waiting for server...'
                        })
                    }else{
                        // {type: 'set', path: 'meta.gameover', value: true}
                        let reviewAggregate = get('values.reviewAggregate', read, write, null);
                        // reviewAggregate = {
                        //     total: 3,
                        //     value: 2*5,
                        //     avg: roundTo(1/3*5, 2)
                        // }
                        //game.review
                        let review = get('review', read, write, null);
                        // review = {
                        //     reviewScore: 5,
                        //     fee: 300,
                        //     tip:30
                        // }
                        if(!review || !reviewAggregate){
                            sendError(`invalid endgame state review ${review} or reviewAggregate ${reviewAggregate} 
                                is not defined`);
                            throw Error(`Invalid game state`);
                        }
                        let dialogue;
                        if(reviewAggregate.avg > keys.reviewThreshold){
                            dialogue = getDialogue('dgGoodEnd');
                            return <Dialogue {...getPar(settings, viewProps, makeChange, `dgGoodEnd`)}
                            dialogue={dialogue} 
                            //move="hub"
                            onExit={(id,v)=>{
                                return [{type: 'set', path: 'meta.gameover', value: true}]
                            }}
                            write={write}
                            read={read}
                            addons={dialogue.pull(read, write, review, reviewAggregate)}
                        />
                        }else{
                            dialogue = getDialogue('dgBadEnd');
                            return <Dialogue {...getPar(settings, viewProps, makeChange, `dgBadEnd`)}
                            dialogue={dialogue} 
                            //move="hub"
                            onExit={(id,v)=>{
                                return [{type: 'set', path: 'meta.gameover', value: true}]
                            }}
                            write={write}
                            read={read}
                            addons={dialogue.pull(read, write, review, reviewAggregate)}
                        />
                        }
                        
                    }

                }
            }
        },
    }
}

const output = o;
export default output;