import React from 'react';
import {get, DELETED} from 'utilities/Merge';
import Text from 'game/Views/TextManager/TextManager';
import Scene from 'game/Views/Scene/Scene.js';
import {log} from 'utilities/Measure';
import {MCQ,Operation, GaEvent, Dialogue} from 'utilities/Create.js';
import {getEqSide, getImg, roundTo, listToEnglish, shuf} from 'utilities/Format.js';
import {operate, EQLR, expand, toLatex, solveFor, toNum, 
        least_common_multiple, getVariables} from 'utilities/Game/MathFun';
import { MULT, POW, DIV, DIFF, doOperation, getConversionFormula, evaluate, 
        interpret, substitute} from 'utilities/Game/TreeMath.js';
import {getEmployeeDescription} from './../texts.js';
import {partialEmployeeView} from './../views.js';
import {getFraction, getMultiple} from 'utilities/Game/Latex';
import {getWorkRateEqs} from './../definitions';
import keys from './../keys';

export const dgWorkRateTheory = {
    key: 'dgWorkRateTheory',
    pull: (read, write) => {
        let workUnitEqs = [
            { eq: `1/t`, latex: toLatex(`1/t`)},
            { eq: `t`, latex: toLatex(`t`)},
            { eq: `1^t`, latex: toLatex(`1^t`)},
        ]
        let workRateEqs = [
            `1/x = 1/t1 + 1/t2 + 1/t3`,
            `1/x = -1/t1 - 1/t2 - 1/t3`,
        ]
        workRateEqs = workRateEqs.map(eq => {
            return {eq, latex: toLatex(eq)}
        });
        return {workUnitEqs, workRateEqs}
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        let workUnitEqs = write.temp.workUnitEqs;
        let workUnitEq = write.temp.workUnitEq;
        let workRateEqs = write.temp.workRateEqs;
        let workRateEq = write.temp.workRateEq;
        /*
        keys.tForm
        keys.WRTdenomFracVal
        keys.WRTcombinedVsIndiv
        keys.wrtOperation
        */
        switch(node){
            case 'siteSize': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `Not quite...`},
                        {latex: workRateEq.latex},
                        {text: `tells me how long it will take to break down 1 sample. I need to convert
                        it to the estimated site size to know how long the whole job will take...`}
                    ]},
                    {...keys.getSis('sis0', 0, 2, null, 'aR')},
                ],
                choice: {options: [
                                    <Text text={"next"}  out={[
                                            {end: true}
                                        ]} 
                                    />
                                ]
                        } 
                });
            case 'willWork': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL')},
                    {convoId: 'dgWorkRateTheory', textId: node,
                    ...keys.getSis('sis0', 0, 1, null, 'aR'),
                    text: [
                        {text: `So this will tell you when we'll finish the work?`},
                    ]},
                ],
                choice: {options: [
                                    <Text text={"next"}  out={[
                                            {type: 'set', path: 'temp.node', value: 'siteSize'}
                                        ]} 
                                    />
                                ]
                        } 
                });
            case 'workRateEqRight': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `Yes, their work rates will sum together. The equation`},
                        {latex: workRateEq.latex},
                        {text: `should let me know how long they'll take to break down 1 unit of work together.`}
                    ]},
                    {...keys.getSis('sis0', 0, 1, null, 'iR')},
                ],
                choice: {options: [
                                    <Text text={"next"}  out={[
                                            {type: 'set', path: 'temp.node', value: 'willWork'}
                                        ]} 
                                    />
                                ]
                        } 
                });
            case 'workRateEqWrong': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `Mmmm that'd be close, but what does it mean that the fairies work rates are negative? 
                        What would it mean to get work done in negative time? It doesn't make sense.`},
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR')},
                ],
                choice: {options: [
                                    <Text text={"next"}  out={[
                                            {type: 'set', path: 'temp.node', value: 'combinedFaster'}
                                        ]} 
                                    />
                                ]
                        } 
                });
            case 'combinedFaster2':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `So I'm trying to find out how their respective work times, 
                            which I'll label as t1, t2 and t3, add up to their combined time, 
                            which I'll label x. The formula should be...`}
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR')},
                ],
                choice: {options: workRateEqs.map((eq, i)=> {
                            return <Text {...eq}  out={[
                                    {type: 'set', path: 'temp.node', value: i === 0?'workRateEqRight':'workRateEqWrong'},
                                    {type: 'set', path: 'temp.workRateEq', value: eq},
                                    MCQ(keys.acGameMCQ, keys.wrtOperation, i===0?'sum':'subtract', i === 0, i === 0?null:[
                                        {text: `Player had to decide how individual work rates would combine to a single work rate, the player picked`},
                                        {latex: eq.latex},
                                        {text: `but the answer was`},
                                        {latex: workRateEqs[0].latex}
                                    ])
                                ]} 
                            />
                        })
                    } 
                });
            case 'combinedFaster':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `So they'll be faster together`}
                    ]},
                    {...keys.getSis('sis0', 0, 1, null, 'iR')},
                ],
                choice: {options:  [
                            <Text text={"next"}  out={[
                                {type: 'set', path: 'temp.node', value: 'combinedFaster2'}
                            ]} 
                        />
                        ]
                    } 
                });
            case 'combinedSlower':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL')},
                    {convoId: 'dgWorkRateTheory', textId: node,
                    ...keys.getSis('sis0', 0, 2, null, 'aR'),
                    text: [
                        {text: `Why would we be slower together? We'll each be breaking down the site at our normal rates. 
                                Three fairies are faster than one.`}
                    ]},
                ],
                choice: {options: [
                                    <Text text={"next"}  out={[
                                            {type: 'set', path: 'temp.node', value: 'willCombine'}
                                        ]} 
                                    />
                                ]
                        } 
                });
                case 'willCombine':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                        text: [
                            {text: `Right. All three of you will be breaking down the contaminants together. 
                            I expect your combined work rates will be...`}
                        ]},
                        {...keys.getSis('sis0', 0, 1, null, 'iR'),},
                    ],
                    choice: {options: [
                                        <Text text={"faster than your individual work rates"}  out={[
                                                {type: 'set', path: 'temp.node', value: 'combinedFaster'},
                                                MCQ(keys.acGameMCQ, keys.WRTcombinedVsIndiv, 'faster', 1, [
                                                    {text: `When asked how fast the fairies combined work rate would be 
                                                    compared to their individual work rates, player incorrectly answered
                                                    "slower than your individual work rates"`}
                                                ])
                                            ]} 
                                        />,
                                        <Text text={"slower than your individual work rates"}  out={[
                                            {type: 'set', path: 'temp.node', value: 'combinedSlower'},
                                            MCQ(keys.acGameMCQ, keys.WRTcombinedVsIndiv, 'slower', 0, [
                                                {text: `When asked how fast the fairies combined work rate would be 
                                                compared to their individual work rates, player incorrectly answered
                                                "slower than your individual work rates"`}
                                            ])
                                        ]} 
                                    />
                                    ]
                            } 
                    });
            case 'largerTimeSlowerWorkErrErr':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `No, when the denominator gets larger, the numerator is cut into a smaller number of larger pieces, 
                        making the fraction value smaller. I can review fractions`},
                        {text: 'at khan academy', href: 'https://www.youtube.com/watch?v=kZzoVCmUyKg&t=74s', leave: true}
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR'),},
                ],
                choice: {options: [
                        <Text text={"I'd rather stay and solve the problem"}  out={[
                                {type: 'set', path: 'temp.node', value: 'largerTimeSlowerWorkErr'}
                            ]} 
                        />
                    ]} 
                });
            case 'largerTimeSlowerWorkTest2':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgWorkRateTheory', textId: node,
                        text: [
                            {text: `Right, now back to the problem of representing work rates as fractions!`}
                        ]},
                        {...keys.getSis('sis0', 0, 0, null, 'iR'),},
                    ],
                    choice: {options: [
                            <Text text={`next`}  out={[
                                    {type: 'set', path: 'temp.node', value: 'largerTimeSlowerWork'}
                                ]} 
                            />
                        ]} 
                    });
            case 'largerTimeSlowerWorkTest':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `Right, when the denominator gets larger, the fraction value gets smaller. So for the fractions`},
                        {latex: getFraction(1, 2)},
                        {text: `and`},
                        {latex: getFraction(1, 4)},
                        {text: `I'd expect the fraction value of...`}
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR'),},
                ],
                choice: {options: [
                        <Text text={[
                            {latex: getFraction(1, 2)},
                            {text: ` to be larger than`},
                            {latex: getFraction(1, 4)},
                        ]}  out={[
                                {type: 'set', path: 'temp.node', value: 'largerTimeSlowerWorkTest2'}
                            ]} 
                        />,
                        <Text text={[
                            {latex: getFraction(1, 4)},
                            {text: ` to be larger than`},
                            {latex: getFraction(1, 2)},
                        ]}  out={[
                            {type: 'set', path: 'temp.node', value: 'largerTimeSlowerWorkErrErr'}
                        ]} 
                    />
                    ]} 
                });
            case 'largerTimeSlowerWorkErr':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `Wait, the denominator divides the numerator, so when a denominator gets larger...`}
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR')},
                ],
                choice: {options: [
                        <Text text={"The fraction value gets smaller"}  out={[
                                {type: 'set', path: 'temp.node', value: 'largerTimeSlowerWorkTest'}
                            ]} 
                        />,
                        <Text text={"The fraction value gets larger"}  out={[
                            {type: 'set', path: 'temp.node', value: 'largerTimeSlowerWorkErrErr'}
                        ]} 
                    />
                    ]} 
                });
            case 'largerTimeSlowerWork':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `Since the time taken, t, is in the fraction denominator`},
                        {latex: getFraction(1, 't')},
                        {text: `I expect someone who takes a larger amount of time, t, to finish a job...`}
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR'),},
                ],
                choice: {options: [
                        <Text text={"Will have a smaller work rate"}  out={[
                                {type: 'set', path: 'temp.node', value: 'willCombine'},
                                MCQ(keys.acGameMCQ, keys.WRTdenomFracVal, 'faster', 1, [
                                    {text: `When asked how a larger work period (the denominator) would effect the work rate value, the player
                                    correctly answered "Will have a smaller work rate"`}
                                ])
                            ]} 
                        />,
                        <Text text={"Will have a larger work rate"}  out={[
                            {type: 'set', path: 'temp.node', value: 'largerTimeSlowerWorkErr'},
                            MCQ(keys.acGameMCQ, keys.WRTdenomFracVal, 'slower', 0, [
                                {text: `When asked how a larger work period (the denominator) would effect the work rate value, the player
                                    incorrectly answered "Will have a larger work rate"`}
                            ])
                        ]} 
                    />
                    ]} 
                });
            case 'unitRight':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `Right. The work is divided equally into the amount of seconds or hours taken to finish`}
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR'),},
                ],
                choice: {options: [<Text text={"next"}  out={[
                        {type: 'set', path: 'temp.node', value: 'largerTimeSlowerWork'}
                        ]} 
                    /> ]} 
                });
            case 'unitWrong':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [
                        {text: `The work is divided into however much time, t, it takes to complete it. So `},
                        {latex: workUnitEq.latex}, 
                        {text: `doesn't show that relationship.`}
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR'),},
                ],
                choice: {options: [
                                    <Text text={"next"}  out={[
                                            {type: 'set', path: 'temp.node', value: '1unit'}
                                        ]} 
                                    />
                                ]
                        } 
                });
                case '1unit3': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgWorkRateTheory', textId: node,
                        text: [{text: `I'll label the time taken as t. 
                        The amount of time, t, someone takes to do 1 job is their work rate.
                        How should that work rate be represented numerically?`}]},
                        {...keys.getSis('sis0', 0, 0, null, 'iR'),},
                    ],
                    choice: {options: workUnitEqs.map((eq, i) => {
                        return <Text {...eq} out={[
                                {type: 'set', path: 'temp.node', value: i === 0?'unitRight':'unitWrong'},
                                {type: 'set', path: 'temp.workUnitEq', value: eq},
                                MCQ(keys.acGameMCQ, keys.tForm, 'slower', i===0?100:0, [
                                    {text: `When asked what the best way to represent work spread equally over a period of time t, 
                                    the player answered ${i===0?'in':''}correctly with`},
                                    {latex: eq.latex},
                                    {text: `from the options`},
                                    ...workUnitEqs.map(e => e.latex)
                                ])
                            ]} />
                        })} 
                    });
                case '1unit2': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                        text: [{text: `The longer someone takes to get a job done, the less work 
                        they're doing per second, or hour, or whatever the unit is.`}]},
                        {...keys.getSis('sis0', 0, 0, null, 'iR'),},
                    ],
                    choice: {options: [<Text text="next" out={[
                        {type: 'set', path: 'temp.node', value: '1unit3'}
                    ]} />]} 
                    });
            case '1unit': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgWorkRateTheory', textId: node,
                    text: [{text: `Whatever the sample is, it'll be 1 unit of work. 
                    The work is divided by the amount of time taken to break it down`}]},
                    {...keys.getSis('sis0', 0, 0, null, 'aR')},
                ],
                choice: {options: [<Text text="next" out={[
                    {type: 'set', path: 'temp.node', value: '1unit2'}
                ]} />]} 
                });
            case 'sampleUnit2':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),  convoId: 'dgWorkRateTheory', textId: node,
                    text: [{text: `Something small that you can experiment with. A few grams of the substance you'll be breaking down would do. 
                    I'll calculate the unit conversions later.`}]},
                    {...keys.getSis('sis0', 0, 0, null, 'aR'),},
                ],
                choice: {options: [
                            <Text text={"next"}  out={[
                                    {type: 'set', path: 'temp.node', value: '1unit'}
                                ]} 
                            />
                        ]
                    }});
            case 'sampleUnit':return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgWorkRateTheory', textId: node,
                    text: [{text: `It'll be whatever sample size you collect from the site. `}]},
                    {...keys.getSis('sis0', 0, 0, null, 'aR'),},
                ],
                choice: {options: [
                    <Text text={"next"}  out={[
                        {type: 'set', path: 'temp.node', value: 'sampleUnit2'}
                    ]} 
                />
            ]}});
            case 'whatUnit': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL')},
                    {...keys.getSis('sis0', 0, 2, null, 'aR'),
                    convoId: 'dgWorkRateTheory', textId: node,
                    text: [{text: `What's this unit of work going to be?`}] },
                ],
                choice: {options: [
                    <Text text={"next"}  out={[
                        {type: 'set', path: 'temp.node', value: 'sampleUnit'}
                    ]} 
                />
            ]}});
            default: return Scene('dgWorkRateTheory-start', {settings, viewProps, makeChange,
                audio: [
                    {id: keys.mainTheme, loop: true},
                    {id: "f1-laugh"}
                ],
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'), convoId: 'dgWorkRateTheory', textId: 'tdef1', 
                    text: [{text: `First, I need some way to represent how long it takes to do 1 unit of work` }]},
                    {...keys.getSis('sis0', 0, 1, 'iR', 'aR')},
                ],
                choice: {options: [
                    <Text text={"next"}  out={[
                        {type: 'set', path: 'temp.node', value: 'whatUnit'}
                       // {end:true}
                    ]} 
                />
            ]}});
        }
    }
}

