const dgGenericClient = (next, id, node, read, write, makeChange, settings, viewProps) => {
    let jobSize = write.temp.jobSize;
    switch(node){
        case 'end': return Scene(`dgTeenageBoys-${node}`, {settings, viewProps, makeChange,
            img: [
                {...keys.getSis('Mc', 'Mc', 0, null, 'aL')},
                {...keys.getSis('sis0', 0, 1, null, ['aR']),  convoId: 'mistake', textId: node, 
                text: [
                    {text: `I just hope it tastes good. 
                    ${Math.random() > 0.5?'The last one was delightfully in tune!'
                        :"The last one wasn't synchronizing at all."}`}
                ]}
            ],
            choice: {options: [
                <Text text={"next"}  out={[
                    {type: 'set', path: 'temp.node', value: next}
                ]} 
            />
        ]}});
        case 'sizeAns': return Scene(`dgTeenageBoys-${node}`, {settings, viewProps, makeChange,
            img: [
                {...keys.getSis('Mc', 'Mc', 0, null, 'aL')},
                {...keys.getSis('sis0', 0, 1, null, ['aR']),  convoId: 'mistake', textId: node, 
                text: [
                    jobSize === 0?{text: `No it sounds pretty small`}:null,
                    jobSize === 1?{text: `It sounds normal, so who knows`}:null,
                    jobSize === 2?{text: `The client makes it sound sizeable, so it might take a while`}:null,
                    jobSize === 3?{text: `It certainly sounds like a voluminous job`}:null
                ]}
            ],
            choice: {options: [
                <Text text={"next"}  out={[
                    {type: 'set', path: 'temp.node', value:'end'}
                ]} 
            />
        ]}});
        case 'size': return Scene(`dgTeenageBoys-${node}`, {settings, viewProps, makeChange,
            img: [
                {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'mistake', textId: node, 
                text: [{text: `Is it a particularly large mess?`}]},
                {...keys.getSis('sis0', 0, 1, null, 'aR')}
            ],
            choice: {options: [
                <Text text={"next"}  out={[
                    {type: 'set', path: 'temp.node', value: 'sizeAns'}
                ]} 
            />
        ]}});
        default:
        return Scene('dgGenericClient-default', {settings, viewProps, makeChange,
            img: [
                {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL')},
                {...keys.getSis('sis0', 0, 1, 'iR', 'aR'),  convoId: 'mistake', textId: 'tdef1', 
                text: [{text: `Sounds standard, toxic waste spill that needs discretion.`}]}
            ],
            choice: {options: [
                <Text text={"next"}  out={[
                    {type: 'set', path: 'temp.node', value: 'size'}
                ]} 
            />
        ]}});
    }
}


