import {getWorkRateEqs} from './../definitions';
import keys from './../keys';

export const getRequestText = (read, write) => {
    let requests = get('requests', read, write, {});
    let texts = [], request;
    for(let key in requests){
        request = requests[key];
        texts.push({text: getClientText(request.clientId), className: 'text-underline block', out: [
                    {type: 'set', path: 'meta.requestId',value: request.id},
                    {type: 'set', path: 'move', value: {location: 'work'}}
                ]})
        texts.push({divide: true})
    }
    return texts;
}
/*


    cliCorrosion: 'cliCorrosion',
    cliParanormal: 'cliParanormal',
    cliPlantDisease: 'cliPlantDisease',
    cliMagicMold: 'cliMagicMold',
    cliFratBoys: 'cliFratBoys',
    cliTempFlux: 'cliTempFlux',
*/
const getClientText = (id) => {
    switch(id){
        case keys.cliCorrosion: return `A companies rotating machinery is decaying faster the normal. A lot faster.`;
        case keys.cliParanormal: return `A rather novel request to deal with paranormal thieving activity at the company mines.`;
        case keys.cliPlantDisease: return `A request to deal with a disease that's making infected plants floppy.`;
        case keys.cliMagicMold: return "There's a plea to handle an outbreak of glittering mold. At least it sounds pretty~";
        case keys.cliFratBoys: return "Some teenagers went to the desert to party...";
        case keys.cliTempFlux: return "A company is experiencing abnormal temperature fluctations at their dig site.";
        default:
            return `A standard sounding request, nothing all that noteable about it.`
    }
}