export const dgGoodEnd = {
    key: 'dgGoodEnd',
    pull: (r, w, review, reviewAggregate) => {
        //get review aggregate and last review
        return {review, reviewAggregate}
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        let review = write.temp.review;
        let reviewAggregate = write.temp.reviewAggregate;
        switch(node){
            case 'toxicGood': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1, 'iL', 'aL')},
                    {...keys.getSis('sis0', 0, 1, 'iR', 'aR'), convoId: 'endScene', textId: node, 
                    text: [{text: `You need the 'magic' to understand, little sister.`}]}
                ],
                choice: {options: [
                    <Text text={"great"} out={{end: true}} />
                ]
            }});
            case 'revenue': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: `You'd do this just for the toxic waste wouldn't you?` },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'toxicGood'}} />
                ]
            }});
            case 'excellent': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1, 'iL', 'aL')},
                    {...keys.getSis('sis0', 0, 1, 'iR', 'aR'), convoId: 'endScene', textId: node, 
                    text: [{text: `Excellent! I'll let the other fairies know the feast won't be interrupted by your 'standards'.`}]}
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'revenue'}} />
                ]
            }});
            case 'proceed': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: `I am happy to proceed with this business scheme of yours.` },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'excellent'}} />
                ]
            }});
            case 'goodStart3':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: [
                        {text: `My calculations of when you'd finish the jobs were good enough!`}
                    ] },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'proceed'}} />
                ]
            }});
            case 'goodStart2':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: [
                        review.reviewScore > reviewAggregate.avg?
                            {text:`That last review of ${review.reviewScore}, was even better!.`}
                            :{text:`The last review of ${review.reviewScore} wasn't as good, but we're doing well.`}
                    ] },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'goodStart3'}} />
                ]
            }});
            case 'goodStart': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: `After ${reviewAggregate.total} reviews, we got an average of ${reviewAggregate.avg}. The revenue is good too.` },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'goodStart2'}} />
                ]
            }});
            default:
                return endIntro('goodStart', id, node, read, write, makeChange, settings, viewProps)
        }
    }
}

export const dgBadEnd = {
    key: 'dgBadEnd',
    pull: (r, w, review, reviewAggregate) => {
        return {review, reviewAggregate}
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        console.log(`dialogue ${id} write`, write);
        let review = write.temp.review;
        let reviewAggregate = write.temp.reviewAggregate;
        switch(node){
            case 'thanks': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1, 'iL', 'aL')},
                    {...keys.getSis('sis0', 0, 1, 'iR', 'aR'), convoId: 'endScene', textId: node, 
                    text: [{text: `You know me! Thanks for trying anyway. I thought you did great.`}]}
                ],
                choice: {options: [
                    <Text text={"great"} out={{end: true}} />
                ]
            }});
            case 'legal':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: `Just keep it legal.` },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'thanks'}} />
                ]
            }});
            case 'creative': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1, 'iL', 'aL')},
                    {...keys.getSis('sis0', 0, 1, 'iR', 'aR'), convoId: 'endScene', textId: node, 
                    text: [{text: `Fine... I'll let the other fairies know we'll 
                    have to get more creative access to the companies accidents.`}]}
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'legal'}} />
                ]
            }});
            case 'standards':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: `No. I have minimum standards. 
                    Sorry if that's an estranged concept to you, ${keys.sis0}. 
                    This business experiment is over.` },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'creative'}} />
                ]
            }});
            case 'sadNews': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1, 'iL', 'aL')},
                    {...keys.getSis('sis0', 0, 1, 'iR', 'aR'), convoId: 'endScene', textId: node, 
                    text: [{text: `Ah...you know you could relax for once and just enjoy the 'work', 
                    even if the clients don't...`}]}
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'standards'}} />
                ]
            }});
            case 'badStart3':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: [
                        {text: `My calculations of when you'd complete the jobs 
                        were just too inaccurate for the clients satisfaction.`}
                    ] },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'sadNews'}} />
                ]
            }});
            case 'badStart2':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: [
                        review.reviewScore > reviewAggregate.avg?
                            {text:`The last review of ${review.reviewScore}, was better, but it's not enough.`}
                            :{text:`The last review of ${review.reviewScore}, was worse than average.`}
                    ] },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'badStart3'}} />
                ]
            }});
            case 'badStart':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1,  null, 'aL'),
                    convoId: 'endScene', textId: node, 
                    text: `After ${reviewAggregate.total} reviews, we got an average of ${reviewAggregate.avg}...` },
                    {...keys.getSis('sis0', 0, 1, null, 'aR')},
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'badStart2'}} />
                ]
            }});
            default:
                return endIntro('badStart', id, node, read, write, makeChange, settings, viewProps)
        }
    }
}

const endIntro = (next, id, node, read, write, makeChange, settings, viewProps) => {
    switch(node){
        case 'tellMe': return Scene(`dgBadEnd-${node}`, {settings, viewProps, makeChange,
            img: [
                {...keys.getSis('Mc', 'Mc', 1, 'iL', 'aL')},
                {...keys.getSis('sis0', 0, 1, 'iR', 'aR'), convoId: 'endScene', textId: node, 
                text: [{text: `What's the verdict, ${keys.sisMc}?`}]}
            ],
            choice: {options: [
                <Text text={"great"} out={{type: 'set', path: 'temp.node', value: next}} />
            ]
        }});
        default:
            return Scene('dgBadEnd-default', {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 1, 'iL', 'aL'), convoId: 'endScene', textId: 'start', 
                    text: [{text: `Alright, we've got enough reviews to see if this venture is worth my time.`}]},
                    {...keys.getSis('sis0', 0, 1, 'iR', 'aR')}
                ],
                choice: {options: [
                    <Text text={"great"} out={{type: 'set', path: 'temp.node', value: 'tellMe'}} />
                ]
            }});
    }
}