import keys from './../keys';



export const dgWin = {
    key: `dgWin`,
    move: 'endgame',
    pull: (read, write) => {
        let reviewScore = get('game.values.reviewAvg', read, write, 58);
        return {reviewScore};
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        switch(node){
            default: return Scene(`dgWin`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: 'tdef1', text: [
                        {text: `We won`},
                    ]},
                ],
                choice: {options: [<Text text={"next"}  out={{end: true}} />]}});
        }
    }
}

