import React from 'react';
import {get, DELETED} from 'utilities/Merge';
import Text from 'game/Views/TextManager/TextManager';
import Scene from 'game/Views/Scene/Scene.js';
import {log} from 'utilities/Measure';
import {MCQ,Operation, GaEvent} from 'utilities/Create.js';
import {getEqSide, getImg, roundTo, listToEnglish, shuf} from 'utilities/Format.js';
import {operate, EQLR, expand, toLatex, solveFor, evaluate, toNum, 
        least_common_multiple, getVariables} from 'utilities/Game/MathFun';
import { MULT, POW, DIV, DIFF, doOperation} from 'utilities/Game/TreeMath.js';
import {getEmployeeDescription} from './../texts.js';
import {partialEmployeeView} from './../views.js';
import {getFraction} from 'utilities/Game/Latex';
import {getWorkRateEqs} from './../definitions';
import keys from './../keys';

export const dgEmployeeSection = {
    key: 'dgEmployeeSection',
    pull: (read, write) => {
        let employees = get('employees', read, write, null, true);
        let lastEmployee = get('meta.newEmployee', read, write, null, true);
        let employeeNames = shuf(Object.keys(employees));
        employeeNames = employeeNames.filter(f => employees[f].hasOwnProperty(keys.pPwr));
        let index = 0;
        if(lastEmployee && employeeNames.indexOf(lastEmployee) > -1){
            index = employeeNames.indexOf(lastEmployee);
        }
        return {employeeNames, employees, index};
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
     
        let name = write.temp.employeeNames[write.temp.index];
        let employee = write.temp.employees[name];
        switch(node){
            default: return Scene(`dgEmployeeSection-start-${name}`, {
                settings, viewProps, makeChange,
                ...partialEmployeeView(employee, viewProps),
                choice: {
                    options: [
                        <Text text="Back" out={{end: true}} />,
                        <Text disabled={name === keys.nSt} text="Retry" out={name === keys.nSt?null:[
                                {type: 'set', path: 'temp.retry', value: name},
                                {end:true},
                            ]} />,
                        <Text text={`Next ${write.temp.index + 1}/${write.temp.employeeNames.length}`} out={[
                            {type: 'set', path: 'temp.index', 
                                value: (write.temp.index+1)%write.temp.employeeNames.length}
                        ]} />,
                    ]
                }
            })
        }
    }
}