
var keys = require('./keys');

const workers = [
    keys.nBe, keys.nSh, keys.nRh, keys.nNe, keys.nJo
];
const SPEC_RAD = 'radiation'
const SPEC_VIB = 'vibration';
const SPEC_DARK = 'dark';
const SPEC_MOLD = 'mold';


const SPECIALIZATIONS = [SPEC_RAD, SPEC_VIB, SPEC_DARK, SPEC_MOLD];

const workerSpecialties = {
    [keys.nSt]: [SPEC_MOLD],
    [workers[0]]: [SPEC_VIB, SPEC_RAD],
    [workers[1]]: [SPEC_RAD],
    [workers[2]]: [SPEC_DARK, SPEC_MOLD],
    [workers[3]]: [SPEC_MOLD, SPEC_VIB],
    [workers[4]]: [SPEC_RAD, SPEC_DARK, SPEC_VIB],
}
const getTotalWork = (ray) => {
    let sum = ray.reduce((a, e, i) => {
        return a + 1/e;
    }, 0);
    return Math.pow(sum, -1)
}

const SIS_WORK_RANGES = [
    [3, 6],
    [7, 12],
    [12, 18]
]

//const totalWorkRangeMin = getTotalWork([SIS_WORK_RANGES[0][0], SIS_WORK_RANGES[1][0], SIS_WORK_RANGES[2][0]]);
const SCORE_RANGE_COMBINED_WORK = getTotalWork([SIS_WORK_RANGES[0][1], SIS_WORK_RANGES[1][1], SIS_WORK_RANGES[2][1]]);
const SCORE_RANGE_HOURS = 5;

const convert = (value, fromUnit, toUnit) => {
    switch(fromUnit){
        case keys.uGr:
            if(toUnit === keys.uKi){
                return value*0.001;
            }
    }
}


var customerIds;
const getCustomerId = (format) => {
    if(!customerIds){
        customerIds = format.shuf([keys.cliCorrosion, keys.cliParanormal, keys.cliPlantDisease, keys.cliTempFlux, keys.cliMagicMold, keys.cliFratBoys])
    }
    let id;
  
    if(customerIds.length > 0){
        id = customerIds.pop();
    }else{
        id = 'customerGeneric';
    }
    console.log(`sim create getCustomerId ${customerIds.length} id ${id}`)
    return id;
}
const getJobSizes = (clientId, randomize) => {
    return {
        sampleSize: {value: randomize.getRange(5, 15), unit: keys.uGr},
        siteSize: {value: randomize.getRange(50, 150), unit: keys.uKi}
    }
}

const createToxicWasteRequest = (id, flags, format, randomize, latex) => {
    //let clientId = getCustomerId(flags);

    let sistersWorkRates = [randomize.getRange(SIS_WORK_RANGES[0][0], SIS_WORK_RANGES[0][1], 0), 
                            randomize.getRange(SIS_WORK_RANGES[1][0], SIS_WORK_RANGES[1][1], 0), 
                            randomize.getRange(SIS_WORK_RANGES[2][0], SIS_WORK_RANGES[2][1], 0)];
    sistersWorkRates = sistersWorkRates.map(wr => format.roundTo(wr, 2));
 //  let order = [0, 1, 2]; //determines which sister discoveres what
    //combined work rate for all three, used in combined work rate problem
    let combinedTime = getTotalWork(sistersWorkRates);
    let clientId = getCustomerId(format);
    let jobSizes = getJobSizes(clientId, randomize);
    let sampleSizeInSiteUnits = convert(jobSizes.sampleSize.value, jobSizes.sampleSize.unit, jobSizes.siteSize.unit);
    let siteSeconds = (combinedTime*jobSizes.siteSize.value)/sampleSizeInSiteUnits;
    let siteHours = siteSeconds/(60*60);
    let game = {
            id, clientId, //sisterOrder: order, 
            sistersWorkRates, ...jobSizes
        };
    return {
        game,
        sim: {
            ...game,
            sistersWorkRates,
            combinedTime,
            siteSeconds,
            siteHours
        }
    }
}

const getNewRequests = (num, state, flags, format, randomize, latex) => {
    state.game.requests = state.game.requests || {}
    state.sim.requests = state.sim.requests || {}
    let jobs = Object.keys(state.sim.requests), job;
    while(jobs.length < num){
        jobs.push(randomize.getHash());
    }
    jobs = jobs.map(j => {
        if(!state.sim.requests[j]){
            job = createToxicWasteRequest(j, state.game.flags, format, randomize, latex);
            console.log(`sim created job customerIds ${customerIds}`, job);
            state.game.requests[j] = job.game; 
            state.sim.requests[j] = job.sim;
        }
    })
    return state;
}



var sim =  {
    //(settings, create, generate, format, math)
    simInit: (settings, create, randomize, format, math, score, iterators, latex) => {
        let state = new create.GameInstance('intro');

        state.game.meta.simFields = ['values', 'requests', 'reviews', 'flags', 'mcq'];

        state.sim.scores.combinedRate = create.AssessmentScore('combinedRate', 2, 5, 1);
        state.sim.scores.combinedRateLatest = create.AssessmentScore('combinedRateLatest', 2, 5, 1);
        //state.sim.scores.soloRate = create.AssessmentScore('soloRate', 0, 0, 1);
        state.sim.scores.secondsToHours = create.AssessmentScore('secondsToHours', 0, 0, 1);
        state.sim.scores.secondsToHoursLatest = create.AssessmentScore('secondsToHoursLatest', 0, 1, 1);

        state.game.meta.numReviews = 3;
        state.sim.numReviews = 3;
        state.sim.money = 500;
        state.game.values.money = {value: 500, unit: keys.uDollar};
        state.sim.reviewAggregate = {total: 0, value: 0, avg: 0};
        state.game.values.reviewAggregate = state.sim.reviewAggregate;
       
        //make initial job involving 
        state.game.requests = { }
        state.sim.requests = { }
        getNewRequests(3, state, state.game.flags, format, randomize, latex);
        console.log(`simInit state`, state);
        return state;
    },
    testLoop: function(inputs, state, settings, create, randomize, format, math, score, iterators, latex){
        let change = {};
        change[`game.values.reviewAggregate`] = {
            total: 10, value: 47, avg: 4.7
        };
        change[`game.meta.move.location`] = 'endgame';
        return change;
    },
    loop: function(inputs, state, settings, create, randomize, format, math, score, iterators, latex){
        let change = {};
        let flags = state.game.flags;
        let money = state.sim.money;
        let reviewAggregate = {...state.sim.reviewAggregate};

        //iterate through requests, grading any that have combinedTime property
        let requests = state.sim.requests;
        let kRequests = Object.keys(requests);
        let sRequest, uRequest, review = {};
        let scoreCombinedRate = {...state.sim.scores.combinedRate};
        let scoreCombinedRateLatest = {...state.sim.scores.combinedRateLatest};
        //let scoreSoloRate = {...state.sim.scores.soloRate};
        let secondsToHours = {...state.sim.scores.secondsToHours};
        let secondsToHoursLatest = {...state.sim.scores.secondsToHoursLatest};
        kRequests.map(k => {
            sRequest = requests[k];
            uRequest = state.game.requests[k];
            review = {...uRequest};
            if(uRequest.hasOwnProperty('combinedTime')){
                //the job has been completed, remove it
                delete state.sim.requests[k];
                delete state.game.requests[k];
                //compare sim.SistersWorkRates to game.sistersWorkRates, except for the first one
                // let sSisWR = sRequest.sistersWorkRates.slice(1);
                // let uSisWR = uRequest.sistersWorkRates.slice(1);
               // let scores = [];
                //for user feedback client side
                // let answers = [];
                // let userAnswers = [];
                let scoreValue = 0, hoursScore = 0;
                //mark the solo work rates
                // sSisWR.map((e, i) => {
                //     scoreValue = score.scoreDistance(e, SIS_WORK_RANGES[i][0], uSisWR[i]);
                //     scoreSoloRate.total += 1;
                //     scoreSoloRate.score += scoreValue;
                //     scoreSoloRate.notes.push(`Player thought sisters work rate for job ${uRequest.clientId} 
                //             was ${format.roundTo(uSisWR[i], 2)}, but it was ${e}. They scored ${scoreValue}.`)
                //     scores.push(scoreValue);
                //     answers.push(format.roundTo(e, 2));
                //     userAnswers.push(format.roundTo(uSisWR[i], 2));
                // })
                //mark the combined work rates
                scoreValue = score.scoreDistance(sRequest.combinedTime, SCORE_RANGE_COMBINED_WORK, uRequest.combinedTime);
               //  scores.push(scoreValue);
                 scoreCombinedRate = {...state.sim.scores.combinedRate};
                 scoreCombinedRate.total += 1;
                 scoreCombinedRate.score += scoreValue;
                 scoreCombinedRate.notes.push(`Player calculated the fairies combined 
                    work rate to be ${format.roundTo(uRequest.combinedTime, 2)}
                    when the answer was ${format.roundTo(sRequest.combinedTime, 2)}`);
                scoreCombinedRateLatest.total =1;
                scoreCombinedRateLatest.score = scoreValue;
                review.simCombinedTime = sRequest.combinedTime;
                review.scoreCombinedTime = scoreValue;
   
                // scoreSoloRate.total += 1;
                // scoreSoloRate.score += scoreValue;
                // scoreSoloRate.notes.push(`Player thought the sisters combined work rate for job ${uRequest.clientId} 
                //             was ${format.roundTo(uRequest.combinedTime, 2)}, but it was ${format.roundTo(sRequest.combinedTime, 2)}. They scored ${scoreValue}.`)
                //  answers.push(format.roundTo(sRequest.combinedTime, keys.roundWeight));
                // userAnswers.push(format.roundTo(uRequest.combinedTime, keys.roundWeight));
            
                //mark the estimate in hours
                //siteHours vs solutionHours
                review.simSolutionHours = sRequest.siteHours;
                hoursScore = score.scoreDistance(sRequest.siteHours, SCORE_RANGE_HOURS, uRequest.solutionHours);
                review.scoreSolutionHours = hoursScore;
                // answers.push(format.roundTo(sRequest.siteHours, keys.roundTime));
                // userAnswers.push(format.roundTo(uRequest.solutionHours, keys.roundTime));
              
                //scores.push(hoursScore);
                secondsToHours.total += 1;
                secondsToHours.score += hoursScore;
                secondsToHours.notes.push(`The player had to convert their answer, ${format.roundTo(uRequest.combinedTime, 2)} seconds,
                for the combined time to break down the sample ${sRequest.sampleSize.value}${sRequest.sampleSize.unit}
                to the estimated amount of time it'd take to break down ${sRequest.siteSize.value}${sRequest.siteSize.unit}.
                The player answered ${format.roundTo(uRequest.solutionHours, 2)} hours, 
                the answer was ${format.roundTo(sRequest.siteHours)} hours.`);
                //so the user gets rewarded if they improve
                secondsToHoursLatest.total = 1;
                secondsToHoursLatest.score = hoursScore;
               

                //review object for user feedback
                //calculate the review score from the estimated hours
                review.reviewScore = format.roundTo(hoursScore*5, 2);
                reviewAggregate.total += 1;
                reviewAggregate.value += review.reviewScore;
                reviewAggregate.avg = format.roundTo(reviewAggregate.value/reviewAggregate.total);
               // review.scores = scores;
                // review.answers = answers;
                // review.userAnswers = userAnswers;
                //calculate money earned
                review.fee = 300;
                review.tip = review.fee*2*review.reviewScore;
                console.log(`sim sRequest`, sRequest);
                console.log(`sim uRequest`, uRequest);
                console.log(`sim review`, review);
                change[`sim.money`] = state.sim.money +  review.fee +  review.tip;
                change[`game.values.money`] = {value: change[`sim.money`], unit: keys.uDollar};
                //update the game state
                change[`game.review`] = review;
                change[`sim.reviewAggregate`] = reviewAggregate;
                change[`game.values.reviewAggregate`] = reviewAggregate;
                console.log(`sim review total ${reviewAggregate.total} numReviews ${state.sim.numReviews}`, state);
                if(reviewAggregate.total >= state.sim.numReviews){
                    change[`game.move.location`] = 'endgame';
                }
                
            }
        })

        getNewRequests(3, state, state.game.flags, format, randomize, latex);
        change[`game.requests`] = state.game.requests;
        change[`sim.requests`] = state.sim.requests;
        change[`sim.scores.combinedRate`] = scoreCombinedRate;
        change[`sim.scores.combinedRateLatest`] = scoreCombinedRateLatest;
        change[`sim.scores.secondsToHours`] = secondsToHours;
        change[`sim.scores.secondsToHoursLatest`] = secondsToHoursLatest;
      //  change[`sim.scores.soloRate`] = scoreSoloRate;
        return change;
    },
}
module.exports = sim;











const getClientId = (specs, flags) => {
    //if all special missions have flags set, just use generic
    let id = null;
    if(!flags[keys.cliFratBoys] && specs.length === 2 
        && specs.indexOf(SPEC_RAD) >  -1 && specs.indexOf(SPEC_VIB)){
        id = keys.cliFratBoys;
    }else if(!flags['dgRadMo1'] && specs.length === 2 
        && specs.indexOf(SPEC_RAD) >  -1 && specs.indexOf(SPEC_MOLD)){
        //radioactive mold chefs
        id = 'dgRadMo1';
    }else if(!flags['dgDaVib1'] && specs.length === 2 
        && specs.indexOf(SPEC_DARK) >  -1 && specs.indexOf(SPEC_VIB)){
        id='dgDaVib1'; //jewellery collector
    }else if(!flags['dgDaMo1'] && specs.length === 2 
        && specs.indexOf(SPEC_DARK) >  -1 && specs.indexOf(SPEC_MOLD)){
        id="dgDaMo1"; //absorbs the light, chefs
    }
    return id;
}
