import {MCQ,Operation, GaEvent, Dialogue} from 'utilities/Create.js';
import {getEqSide, getImg, roundTo, listToEnglish, shuf, getDialogueCategory} from 'utilities/Format.js';
import {operate, EQLR, expand, toLatex, solveFor, toNum, 
        least_common_multiple, getVariables} from 'utilities/Game/MathFun';
import { MULT, POW, DIV, DIFF, doOperation, getConversionFormula, evaluate, 
        interpret, substitute} from 'utilities/Game/TreeMath.js';
import {getEmployeeDescription} from './../texts.js';
import {partialEmployeeView} from './../views.js';
import {getFraction, getMultiple} from 'utilities/Game/Latex';
import {getWorkRateEqs} from './../definitions';
import MathAlgorithms from 'utilities/Math/Algorithms';
import keys from './../keys';

const buildEq = (wr1, wr2, combined) => {
    return {
        eq: `1/${combined}=1/${wr1}+1/${wr2}`,
        latex: `${getFraction(1, combined)}=${getFraction(1, wr1)}+${getFraction(1, wr2)}`
    };
}

export const dgSampleSiteSize = {
    key: 'dgSampleSiteSize',
    pull: (r, w) => {
        let unitVariable = 'x';
        let sampleSize = {value: 10, unit: keys.uGr};
        let siteSize = {value: 500, unit: keys.uKi};
        let unitConversionEqs = getConversionFormula(sampleSize.value, sampleSize.unit, siteSize.unit, 2, unitVariable);
        
        return {sampleSize, siteSize, unitConversionEqs, unitVariable}
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        console.log(`dialogue dgSampleSiteSize node ${node} write`, write);
        let sampleSize = write.temp.sampleSize;
        let siteSize = write.temp.siteSize;
        let unitVariable = write.temp.unitVariable;
        let unitConversionEqs = write.temp.unitConversionEqs;
        let siteToSampleValue = write.temp.siteToSampleValue;
        let siteToSampleRatioEqs = write.temp.siteToSampleRatioEqs;
        let ratio = write.temp.ratio;
        let timeRatioEqs = write.temp.timeRatioEqs;
        let timeRatioEq = write.temp.timeRatioEq;
        let gramsInKilograms = write.temp.gramsInKilograms;
        let ansGramsInKilograms = write.temp.ansGramsInKilograms;

        switch(node){
            case 'secondsToHours2': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
                    textId: node, text: [
                        {text: `And the more accurate my answer is for when the fairies will finish the job, the 
                            higher the review score will be`}
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR')},
                ],
                choice: {options: [
                            <Text text="next"  out={{end: true}} />
                        ]
                    } 
                });
            case 'secondsToHours': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
                    textId: node, text: [
                        {text: `The problem is, I'm guessing the clients don't want their answer in seconds. 
                        I'll need to then convert the answer from seconds to hours...`}
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR')},
                ],
                choice: {options: [
                            <Text text="next"  out={{type: 'set', path: 'temp.node', value: 'secondsToHours2'}} />
                        ]
                    } 
                });
            case 'timeRight': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
                        textId: node, text: [
                            {text: `Okay. So I need to`},
                            {latex: timeRatioEq.latex},
                            {text: `find out how to convert the seconds taken to break down the samples, to the whole job.`}
                        ]},
                        {...keys.getSis('sis0',0, 0, null, 'iR')},
                    ],
                    choice: {options: [
                                <Text text="next"  out={{type: 'set', path: 'temp.node', value: 'secondsToHours'}} />
                            ]
                        } 
                    });
            // case 'timeWrong': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
            //         img: [
            //             {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
            //             textId: node, text: [
            //                 {text: `If the sample needs to be multiplied by ${ratio} to equal the site size, then time should be...`}
            //             ]},
            //             {...keys.getSis('sis0', 0, 0, null, 'iR')},
            //         ],
            //         choice: {options: [
            //                     <Text text="next"  out={{type: 'set', path: 'temp.node', value: 'siteToSampleRatio'}} />
            //                 ]
            //             } 
            //         });
            // case 'siteToSampleRatio': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
            //     img: [
            //         {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
            //         textId: node, text: [
            //             {text: `So the site's will be a lot larger than the samples, around ${ratio} times as large. 
            //                 So given that ratio, if it takes t seconds to break down the sample, how long will it take to break down the whole site?`}
            //         ]},
            //         {...keys.getSis('sis0', 0, 0, null, 'iR')},
            //     ],
            //     choice: {options: timeRatioEqs.map((eqs, i) => {
            //                 return <Text {...eqs} out={[
            //                     {type: 'set', path: 'temp.node', value: i===0?'timeRight':'timeWrong'},
            //                     {type: 'set', path: 'temp.timeRatioEq', value: eqs},
            //                 ]} />
            //             })
            //         } 
            //     });
            case 'siteToSampleValue':
               
                return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
                    textId: node, text: [
                        {text: `So ${sampleSize.value} ${sampleSize.unit} equals ${ansGramsInKilograms} ${keys.uKi}'s...to
                        figure out the time I need to...`},
                    ]},
                    {...keys.getSis('sis0',0, 0, null, 'iR')},
                ],
                choice: {
                        options: timeRatioEqs.map(eq => {
                            return <Text {...eq} out={[
                                {type: 'set', path: 'temp.node', value: 'timeRight'},
                                {type: 'set', path: 'temp.timeRatioEq', value: eq},
                            ]} />
                        })
                    } 
                });
            case 'gramsInKilograms': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
                        textId: node, text: [
                            {latex: siteToSampleValue.latex},
                            {text: `therefore, ${sampleSize.value} ${sampleSize.unit} 
                                    would equal how many ${siteSize.unit}'s?`},
                        ]},
                        {...keys.getSis('sis0', 0, 0, null, 'iR')},
                    ],
                    choice: {
                        onInput: (id, v) => {
                            let tre = [
                                {eq: `t*${v.value}/${sampleSize.value}`, 
                                    latex: `${getMultiple('t', getFraction(v.value, sampleSize.value))}`},
                                {eq: `t*${sampleSize.value}/${v.value}`, 
                                    latex: `${getMultiple('t', getFraction(sampleSize.value, v.value))}`}
                            ]
                       
                            makeChange(id, [
                                {type: 'set', path: 'temp.node', value: 'siteToSampleValue'},
                                {type: 'set', path: 'temp.ansGramsInKilograms', value: v.value},
                                {type: 'set', path: 'temp.timeRatioEqs', value: tre},
                            ])
                        },
                        options: gramsInKilograms.map((eq, i) => {
                                    return <Text text={`${eq} kg`} value={eq} />
                                })
                            } 
                    });
            case 'formulaSampleToSite': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
                    textId: node, text: [
                        {text: `So what's the formula for converting  ${sampleSize.value} ${sampleSize.unit} 
                                to ${unitVariable} ${siteSize.unit}`},
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR')},
                ],
                choice: {
                    onInput: (id, v) => {
                        let solution = toNum(evaluate(v.eq, {[keys.uKi]: 1}));
                        let gramsInKilograms = [
                            solution, solution*10, solution*0.001, solution*-2
                        ]
                        let ratios = [
                            siteSize.value/solution,
                            siteSize.value/solution*10,
                            siteSize.value/solution*0.001,
                            siteSize.value/solution*-2,
                        ]
                        makeChange(id, [
                            {type: 'set', path: 'temp.siteToSampleRatioEqs', value: ratios},
                            {type: 'set', path: 'temp.node', value: 'gramsInKilograms'},
                            {type: 'set', path: 'temp.gramsInKilograms', value: gramsInKilograms},
                            {type: 'set', path: 'temp.siteToSampleValue', value: v},
                        ])
                    },
                    options: unitConversionEqs.map((eq, i) => {
                                return <Text {...eq}  />
                            })
                        } 
                });
            case 'applesToApples': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
                    textId: node, text: [
                        {text: `I can't figure out how long the whole site will take if the samples are in ${sampleSize.unit},
                            but the total toxic waste is in ${siteSize.unit}`},
                    ]},
                    {...keys.getSis('sis0', 0, 0, null, 'iR')},
                ],
                choice: {options: [
                                <Text text={"next"}  out={[
                                    {type: 'set', path: 'temp.node', value: 'formulaSampleToSite'}
                                    ]} 
                                />
                            ]
                        } 
                });
            case 'siteSize': return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
                        textId: node, text: [
                            {text: `The actual toxic waste site will be much, much larger. 
                            I'll use ${siteSize.value} ${siteSize.unit} as a conservative estimate.`},
                        ]},
                        {...keys.getSis('sis0', 0, 1, null, 'iR')},
                    ],
                    choice: {options: [
                                    <Text text={"next"}  out={[
                                        {type: 'set', path: 'temp.node', value: 'applesToApples'}
                                        ]} 
                                    />
                                ]
                            } 
                    });
            default: return Scene(`dgWorkRateTheory-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'), convoId: 'dgSampleSiteSize', 
                    textId: 'start', text: [
                        {text: `So it'll take a few seconds at least for the fairies to break down a 
                            small toxic sample of say...${sampleSize.value} ${sampleSize.unit}`},
                    ], initial: ['iL'], animate: ['aL']},
                    {...keys.getSis('sis0', 0, 0, 'iR', 'iR')},
                ],
                choice: {options: [
                                <Text text={"next"}  out={[
                                        {type: 'set', path: 'temp.node', value: 'siteSize'}
                                        //{end:true}
                                    ]} 
                                />
                            ]
                        } 
                });
        }
    }
}




