export const dgCombinedWorkRateFormula = Dialogue(
    'dgCombinedWorkRateFormula',
    (read, write)=>{
        return {};
    },
    (id, node, read, write, makeChange, settings, viewProps)=>{
        switch(node){
            default: return Scene(`dgCombinedWorkRateFormula-start`, {
                settings, viewProps, makeChange, 
                img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                        convoId: 'mistake', textId: 'tdef1', text: [
                            {text: `To calculate how quickly they'll all work together, I need all their work rates.`},
                        ]},
                ],
                choice: {options: [
                    <Text text={'next'} out={[ {end:true} ]} />
                ]}
            })
        }
    }
)


const getValue = (item, isLatex) => {
    let val;
    if(isLatex){
        if(item.length === 1){
            val = item[0];
        }else if(item.length === 2){
            val = getMultiple(isNaN(item[0])?item[0]:roundTo(item[0],6), isNaN(item[1])?item[1]:roundTo(item[1], 6));
        }

    }else{
        if(item.length === 1){
            val = item[0];
        }else if(item.length === 2){
            val = `${item[0]}*${item[1]}`;
        }
    }
    console.log(`getProportion getVal item ${item} return`, val);
    return val;
}

const getProportion = (order, values, isLatex) => {
    /**
     * order: ['g', 't', 'k', 's'] //order can be mistaken
     * values: {g: sampleSize.value, s: siteSize.unit...}
     */
    if(isLatex){
        return `${getFraction(getValue(values[order[0]], isLatex), getValue(values[order[1]], isLatex))}
                =${getFraction(getValue(values[order[2]], isLatex), getValue(values[order[3]], isLatex))}`
    }
    return `${getValue(values[order[0]], isLatex)}/${getValue(values[order[1]], isLatex)}
            =${getValue(values[order[2]], isLatex)}/${getValue(values[order[3]], isLatex)}`
}


export const dgUnitConversion = Dialogue(
    'dgUnitConversion',
    (read, write, request) => {
        let flags = get('flags', read, write, null);
        let category = getDialogueCategory(flags,'dgUnitConversion', 'unitConversion');
        console.log(`dialogue dgUnitConversion category ${category} flags`, flags);
        let sampleSize = request.sampleSize;
        let siteSize = request.siteSize;
        let estimatedSeconds = request.combinedTime;
        let estimatedSecondsR = roundTo(estimatedSeconds, keys.roundSec);
       // let unitConversionEqs = getConversionFormula(sampleSize.value, sampleSize.unit, siteSize.unit, 4, `x`);
       let eqMap = [[keys.uGr,keys.uSe,keys.uKi, keys.uHo], [keys.uSe,keys.uGr,keys.uKi,keys.uHo], [keys.uGr,keys.uSe,keys.uHo, keys.uKi]];
       let valueMap = {[keys.uGr]: [sampleSize.value, sampleSize.unit], [keys.uSe]: [estimatedSeconds, keys.uSe],
                    [keys.uKi]: [siteSize.value, siteSize.unit], [keys.uHo]: ['x', keys.uHo]}

        let proportionEqs = eqMap.map(eq => {
            return {
                eq: getProportion(eq, valueMap),
                latex: getProportion(eq, valueMap, true)
            }
        })
        /*
    qUCproportionEq: 'qUCproportionEq',
    qUCsToHEq: 'qUCsToHEq',
    qUCgToKgEq: 'qUCgToKgEq',
    qUCSolveForH: 'qUCSolveForH',
        */
        console.log(`getProportion eqs`, eqMap);
        console.log(`getProportion proportionEqs`, proportionEqs);
        proportionEqs = proportionEqs.map((eq, i) => {
            return {...eq, out: [
                {type: 'set', path: 'temp.node', value: 'conversion'},
                {type: 'set', path: 'temp.selectedProportion', value: eq},
                {type: 'set', path: 'temp.selectedEqMap', value: i},
                MCQ(category, keys.qUCproportionEq, i + "", i===0, [
                    {text: `Player ${i===0?'':'in'}correctly chose the proportion equation`},
                    {latex: eq.latex},
                    // i === 0?null:{text:`but the correct answer was`},
                    // i === 0?null:{latex: proportionEqs.latex},
                ]) 
            ]}
        });
        proportionEqs.push({text: `Think the problem through...`, out: [
            {end: true},
            {type: 'set', path: 'temp.mistake', value:  {key: 'dgProportions', request}},
        ]})
        let secondsToHoursEqs = [
            3600, 36000, 360
        ]
        secondsToHoursEqs = secondsToHoursEqs.map(e => {
            return {eq:`${estimatedSeconds}*(1/${e})`, latex: getMultiple(roundTo(estimatedSeconds, 2), getFraction(1, e)),
                value: estimatedSeconds/e
            }
        })
        secondsToHoursEqs.push({text: `Think the problem through...`, out: [
            {end: true},
            {type: 'set', path: 'temp.mistake', value:  { key: 'dgSecondsToHoursMistake', estimatedSeconds, estimatedSecondsR}},
        ]})
        let gramsToKilogramsEqs = [
            1000, 10, 0.1
        ]
        gramsToKilogramsEqs = gramsToKilogramsEqs.map(e => {
            return {eq:`${sampleSize.value}*(1/${e})`, latex: getMultiple(roundTo(sampleSize.value, 2), getFraction(1, e)), 
                value: sampleSize.value/e
            }
        })
        gramsToKilogramsEqs.push({text: `Think the problem through...`});
        return {category, eqMap, valueMap, modifiedValueMap: valueMap, estimatedSeconds, 
            estimatedSecondsR, request, secondsToHoursEqs, gramsToKilogramsEqs, proportionEqs}
    },
    (id, node, read, write, makeChange, settings, viewProps) => {
        console.log(`dialogue ${id} node ${node} write`, write);
        let request = write.temp.request;
        let category = write.temp.category;
        let unitConversionEqs = write.temp.unitConversionEqs;
        let proportionEqs = write.temp.proportionEqs;
        let secondsToHoursEqs = write.temp.secondsToHoursEqs;
        let gramsToKilogramsEqs = write.temp.gramsToKilogramsEqs;
        let gramsInKilograms = write.temp.gramsInKilograms;
        let convertedWeight = write.temp.convertedWeight;
        let convertedTime = write.temp.convertedTime;
        let sampleSize = write.temp.request.sampleSize;
        let siteSize = write.temp.request.siteSize;
        let estimatedSeconds = write.temp.estimatedSeconds;
        let estimatedSecondsR = write.temp.estimatedSecondsR;
        let selectedProportion = write.temp.selectedProportion;
        let modifiedProportion = write.temp.modifiedProportion;
        let eqMap = write.temp.eqMap;
        let valueMap = write.temp.valueMap;
        let modifiedValueMap = write.temp.modifiedValueMap;
        let selectedEqMap = write.temp.selectedEqMap;
        let hourAlts = write.temp.hourAlts;
        let solutionHours = write.temp.solutionHours;
        console.log(`getProportion modifiedValueMap`, modifiedValueMap)
        switch(node){
            case 'solvedHours2': return Scene(`${id}-${node}`, {
                settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `So if it took ${estimatedSecondsR} seconds to break down ${sampleSize.value} ${sampleSize.unit}'s,
                        I think it'll take ${roundTo(solutionHours, keys.roundTime)} hours to finish a job the size of ${siteSize.value} ${siteSize.unit}'s. 
                        Does that sound right?`}
                    ]},
                ],
                choice: {
                    options: [
                        <Text text="yeah that sounds correct" out={[ {end:true} ]} />,
                        <Text text="No that doesn't sound right, I'll try again" out={[ 
                            {type: 'set', path: 'temp.node', value: 'start'},
                            {type: 'set', path: 'temp.node', value: 'start'},
                            {type: 'set', path: 'temp.modifiedValueMap', value: valueMap},
                            {type: 'set', path: 'temp.modifiedProportion', value: selectedProportion},
                         ]} />,
                    ]
                }
            })
            case 'solvedHours': return Scene(`${id}-${node}`, {
                settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `So if I solve `},
                        {latex: modifiedProportion?modifiedProportion.latex:selectedProportion.latex},
                        {text: `for hours, the answer would be...`}
                    ]},
                ],
                choice: {
                    options: hourAlts.map((v, i) => {
                        return <Text value={v} text={roundTo(v, keys.roundTime) + " hours"} out={[
                            {type: 'set', path: 'temp.node', value: 'solvedHours2'},
                            {type: 'set', path: 'temp.solutionHours', value: v},
                            MCQ(category, keys.qUCSolveForH, i + "", i===0, [
                                {text: `Player solved the equation`},
                                {latex: modifiedProportion?modifiedProportion.latex:selectedProportion.latex},
                                {text: `for hours, and ${i===0?'':'in'}correctly chose 
                                the answer ${roundTo(v, keys.roundTime)} hours from the 
                                options ${hourAlts.map(h => roundTo(h, keys.roundTime))}`}
                            ])
                        ]} />
                    })
                }
            })
            case 'gramsToKilograms': return Scene(`${id}-${node}`, {
                settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `To convert ${sampleSize.value} ${sampleSize.unit}'s to kilograms, I need the equation...`}
                    ]},
                ],
                choice: {
                    onInput: (id, v) => {
                        //valueMap
                        let actions = [],i = v.i;
                        if(v.i === gramsToKilogramsEqs.length - 1){
                            actions= [
                                {end: true},
                                {type: 'set', path: 'temp.mistake', 
                                value:  { key: 'dgGramsToKilogramsMistake', sampleSize, siteSize}},
                            ]
                        }else{
                            let order = eqMap[selectedEqMap];
                            let nValueMap = {...modifiedValueMap};
                            let gtk = evaluate(v.eq);
                            nValueMap[keys.uGr] = [gtk, keys.uKi]
                            let modified = {
                                eq: getProportion(order, nValueMap),
                                latex: getProportion(order, nValueMap, true)
                            }
                            console.log(`getProportion modified order ${order} gtk ${gtk}`, modified)
                            actions = [
                                {type: 'set', path: 'temp.modifiedValueMap', value: nValueMap},
                                {type: 'set', path: 'temp.modifiedProportion', value: modified},
                                {type: 'set', path: 'temp.node', value: 'conversion'},
                                {type: 'set', path: 'temp.gramsInKilograms', value: v.value},
                                MCQ(category, keys.qUCgToKgEq, i + "", i===0, [
                                    {text: `Player ${i===0?'':'in'}correctly thought they could convert 
                                    ${sampleSize.value} grams to kilograms with the operation`},
                                    {latex: v.latex},
                                    i === 0?null:{text:`but the correct answer was`},
                                    i === 0?null:{latex: gramsToKilogramsEqs[0].latex},
                                ])
                            ]
                        }
                        makeChange(id, actions)
                    },
                    options: gramsToKilogramsEqs.map((eq, i) => {
                        return <Text {...eq} i={i} />
                    })
                }
            })
            case 'secondsToHours': return Scene(`${id}-${node}`, {
                settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `To convert ${estimatedSecondsR} seconds to hours, I need the equation...`}
                    ]},
                ],
                choice: {
                    onInput: (id, v) => {
                        //valueMap
                        let actions = [],i = v.i;
                        if(v.i === secondsToHoursEqs.length - 1){
                            actions= [
                                {end: true},
                                {type: 'set', path: 'temp.mistake', 
                                value:  { key: 'dgSecondsToHoursMistake', estimatedSeconds, estimatedSecondsR}},
                            ]
                        }else{
                            let order = eqMap[selectedEqMap];
                            let nValueMap = {...modifiedValueMap};
                            let sth = evaluate(v.eq);
                            nValueMap[keys.uSe] = [sth, keys.uHo]
                            let modified = {
                                eq: getProportion(order, nValueMap),
                                latex: getProportion(order, nValueMap, true)
                            }
                            console.log(`getProportion modified order ${order} sth ${sth}`, modified)
                            actions = [
                                {type: 'set', path: 'temp.modifiedValueMap', value: nValueMap},
                                {type: 'set', path: 'temp.modifiedProportion', value: modified},
                                {type: 'set', path: 'temp.node', value: 'conversion'},
                                {type: 'set', path: 'temp.secondsInHours', value: v.value},
                                MCQ(category, keys.qUCsToHEq, i + "", i===0, [
                                    {text: `Player ${i===0?'':'in'}correctly thought they could convert 
                                    ${estimatedSecondsR} seconds to hours with the operation`},
                                    {latex: v.latex},
                                    i === 0?null:{text:`but the correct answer was`},
                                    i === 0?null:{latex: secondsToHoursEqs[0].latex},
                                ]) 
                            ]
                        }
                        makeChange(id, actions)
                    },
                    options: secondsToHoursEqs.map((eq, i) => {
                        return <Text {...eq} i={i} />
                    })
                }
            })
            case 'conversion': return Scene(`${id}-${node}`, {
                settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `To figure out how many hours it'll take to break the site down, I need to solve`},
                        {latex: modifiedProportion?modifiedProportion.latex:selectedProportion.latex},
                        convertedWeight?{text: `I think the sample size is ${convertedWeight} kilograms.`}:null,
                        convertedTime?{text: `I think time taken to complete the sample in hours is ${convertedTime}`}:null,
                    ]},
                ],
                choice: {
                    onInput: (id, v) => {
                        if(v.i === 0){
                            makeChange(id, [
                                {type: 'set', path:'temp.node', value: 'secondsToHours'}
                            ]);
                        }else if(v.i === 1){
                            makeChange(id, [
                                {type: 'set', path:'temp.node', value: 'gramsToKilograms'}
                            ]);
                        }else if(v.i === 2){
                            let proportion = modifiedProportion?modifiedProportion.eq:selectedProportion.eq;
                            console.log(`conversion solvedHours proportion`, proportion);
                           // let subbed = substitute(proportion)
                            let solved = solveFor(proportion, 'x', true);
                            console.log(`conversion solvedHours solved`, solved);
                            let rhs = solved[0].eq;
                            console.log(`conversion solvedHours rhs`, rhs);
                            let ans = evaluate(rhs, {[keys.uSe]: 1,  [keys.uGr]: 1, [keys.uKi]: 1, [keys.uHo]: 1});
                            ans = toNum(ans);
                            console.log(`conversion solvedHours ans`, ans);
                            let ansAlts = [ans, ans*10, ans*0.1, ans*-2];
                            console.log(`conversion solvedHours ansAlts`, ansAlts);
                            makeChange(id, [
                                {type: 'set', path:'temp.node', value: 'solvedHours'},
                                {type: 'set', path:'temp.hourAlts', value: ansAlts},
                            ]);
                        }
                    },
                    options: [
                        <Text i={0} text="convert the estimated seconds to hours" />,
                        <Text i={1} text="convert the the sample weight to grams"  />,
                        <Text i={2} text="solve for hours"  />
                    ]
                }
            })
            case 'proportion': return Scene(`${id}-${node}`, {
                settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `The equation showing the proportions of the sample to the site would look like...`},
                    ]},
                ],
                choice: {
                    options: proportionEqs.map((eqs, i) => {
                        return <Text {...eqs}  />
                    })
                }
            })
            default: return Scene('dgUnitConversion-estimation', {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                        convoId: 'mistake', textId: 'text-sample', text: [
                            {text: `So the time taken to break down the sample of ${sampleSize.value} ${sampleSize.unit}'s 
                            will be proportional to the time it will take to 
                            break down the whole site of ${siteSize.value} ${siteSize.unit}'s.`},
                        ]},
                    ],
                    choice: {
                        options: [
                            <Text text="next" out={[{type: 'set', path: 'temp.node', value: 'proportion'}]} />
                        ]
                    }
            });
        }
    }
)
