import React from 'react';
import {get, DELETED} from 'utilities/Merge';
import Text from 'game/Views/TextManager/TextManager';
import Scene from 'game/Views/Scene/Scene.js';
import {log} from 'utilities/Measure';
import {MCQ,Operation, GaEvent, Dialogue} from 'utilities/Create.js';
import {getEqSide, getImg, roundTo, listToEnglish, shuf} from 'utilities/Format.js';
import {operate, EQLR, expand, toLatex, solveFor, evaluate, toNum, 
        least_common_multiple, getVariables} from 'utilities/Game/MathFun';
import { MULT, POW, DIV, DIFF, doOperation} from 'utilities/Game/TreeMath.js';
import {getEmployeeDescription} from './../texts.js';
import {partialEmployeeView} from './../views.js';
import {getFraction, getMultiple} from 'utilities/Game/Latex';
import {getWorkRateEqs} from './../definitions';
import keys from './../keys';


export const dgGeneric = (key) =>  {
    return {
        key,
        pull: () => {return {}},
        dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
            return Scene('varOnLhs-start', {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc,
                    imageId: keys.getImg('nMc'), pose: 'neutral',
                    convoId: 'mistake', textId: 'tdef1', text: [
                        {text: `Generic dialogue ${key}`},
                    ], initial: ['pL'], animate: ['aL']},
                ],
                choice: {options: [
                <Text text={"next"}  out={{end: true}} />
            ]}});
        }
    }
}
//dgProportions
export const dgProportions = {
    key: 'dgProportions',
    pull: (read, write, request) => {
        let {combinedTime, sampleSize, siteSize, sistersWorkRates} = request.request;
        combinedTime = roundTo(combinedTime, 2);
        //let sampleRate = toLatex(`${sampleSize.value}*${sampleSize.unit}s/${combinedTime}*seconds`);
        let sampleRate = getFraction(getMultiple(sampleSize.value, sampleSize.unit), getMultiple(combinedTime, 'seconds'))
        let ratioEqs = [
            {
                eq:  `${sampleSize.value}*${sampleSize.unit}/${combinedTime}*${keys.uSe}`,
                latex: getFraction(getMultiple(sampleSize.value, sampleSize.unit), getMultiple(combinedTime, keys.uSe))
            },
            {
                eq: `${combinedTime}*${keys.uSe}/${sampleSize.value}*${sampleSize.unit}`,
                latex: getFraction(getMultiple(combinedTime, keys.uSe), getMultiple(sampleSize.value, sampleSize.unit))
            }
        ]
        let proportionEqs = [
           {
               eq:  `${sampleSize.value}*${sampleSize.unit}/${combinedTime}*${keys.uSe}=${siteSize.value}*${siteSize.unit}/x*${keys.uHo}`,
               latex: `${getFraction(getMultiple(sampleSize.value, sampleSize.unit),getMultiple(combinedTime,keys.uSe))}
                        =${getFraction(getMultiple(siteSize.value,siteSize.unit), getMultiple('x', keys.uHo))}`
           },
           {
               eq:  `${sampleSize.value}*${sampleSize.unit}/${combinedTime}*${keys.uSe}=x*${keys.uHo}/${siteSize.value}*${siteSize.unit}`,
               latex: `${getFraction(getMultiple(sampleSize.value, sampleSize.unit),getMultiple(combinedTime,keys.uSe))}
               =${getFraction(getMultiple('x', keys.uHo), getMultiple(siteSize.value,siteSize.unit))}`
           }
           
        ]
        return {...request.request, sampleRate, proportionEqs, combinedTime, ratioEqs}
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        //sample ratio proportion to site rati
        //since the amount of time they take to break down any toxic waste depends on their work rate
        //I expect the time taken to break the sample down will be proportional to the site
           //a fraction with two different quantities, like weight and time is called a...ratio
        //so when breaking down the sample of 10g per 5 sec, that gives them a ratio of...10/5, 10*5, 10^5
        //and id expect the sample ratio fraction to be equal to prop, gt than, less than the site ratio
        //so the equation should look like...
        //but idont want a range of values, > gives me many values
        //alright,so its the sample weight/time = site weight/time
       
        let {combinedTime, sampleSize, siteSize, sistersWorkRates, sampleRate, proportionEqs, ratioEqs} = write.temp;
        switch(node){
            case 'proportionEq-err2':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `So I can figure out the unknown time it'll take to finish the job. I must be missing something`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'start'}} />
                ]}});
            case 'proportionEq-err':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `Nope, that can't be right, as I'm expecting two rate fractions that are equal to each other`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'proportionEq-err2'}} />
                ]}});
            case 'proportionEq':
                return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `So if the sample and the site rates are proportional to each other, the equation should look like...`}
                    ]},
                ],
                choice: {options: proportionEqs.map((e, i) => {
                    return <Text {...e}  out={[
                        i===0?{end:true}:{type: 'set', path: 'temp.node', value: 'proportionEq-err'},
                        MCQ(keys.acMath, keys.qProportionEq, null, i===0?100:0, [
                            {text: `When asked to identify the proportion eq for the sample ${sampleSize.value} ${sampleSize.unit} done in ${combinedTime} seconds
                            and the site ${siteSize.value} ${siteSize.unit} done in x hours,  
                            player ${i===0?'':'in'}correctly answered`},
                            {latex: e.latex},
                            i===0?null:{text: `when the correct equation was`},
                            i===0?null:{latex: proportionEqs[0].latex},
                        ])
                    ]} />
                })}});
            case 'proportional-err':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `The samples weight of ${sampleSize.value} ${sampleSize.unit}'s 
                        is way smaller than the site size ${siteSize.value} ${siteSize.unit}'s,
                        so the sample can't be larger.`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'proportional'}} />
                ]}});
            case 'proportional':
                let proportionalOptions = [
                    `proportional to the site size`,
                    `greater than the site size`
                ] 
            return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `Okay! and given the site size of ${siteSize.value} ${siteSize.unit}, I expect the sample rate of`},
                        {latex: ratioEqs[0].latex},
                        {text: `to be...`}
                    ]},
                ],
                choice: {options: proportionalOptions.map((p, i) => {
                    return <Text text={p} out={[
                        {type:'set', path: 'temp.node',value: i === 0?'proportionEq':'proportional-err'},
                        MCQ(keys.acMath, keys.qProportionRel, null, i===0?100:0, [
                            {text: `When asked what the relationship between the proportional sample to site was,  
                            player ${i===0?'':'in'}correctly answered, from the options ${p} from the options ${proportionalOptions}`}
                        ])
                    ]} />
                })}});
            case 'rateEq-err':
                    return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                        img: [
                            {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                            convoId: 'mistake', textId: node, text: [
                                {text: `That can't be right. A rate always has time on the bottom, as in 60 miles per hour, 
                                or something similar. So this should be weight/time`}
                            ]},
                        ],
                        choice: {options: [
                            <Text text={"Next"}  out={[
                                {type: 'set', path: 'temp.node', value: 'rateEq'}
                            ]} />
                        ]}});
            case 'rateEq':
                return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                        convoId: 'mistake', textId: node, text: [
                            {text: `Right, and if the sample weight of ${sampleSize.value} ${sampleSize.unit} was finished in ${combinedTime} seconds, 
                            I expect that rate to look like...`}
                        ]},
                    ],
                    choice: {options: ratioEqs.map((p, i) => {
                        return <Text {...p} out={[
                            {type:'set', path: 'temp.node',value: i===0?'proportional':'rateEq-err'},
                            MCQ(keys.acMath, keys.qProportionRateEq, i + "", i===0?100:0, [
                                {text: `When asked what the sample rate would look like,  
                                player ${i===0?'':'in'}correctly answered, from the options ${ratioOptions}`},
                                {latex: p.latex},
                                i===0?null:{text: `when the correct rate was`},
                                i===0?null:{latex: ratioEqs[0].latex}
                            ])
                        ]} />
                    })}});
            case 'ratio-err': 
                return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                        convoId: 'mistake', textId: node, text: [
                            {text: `Close, but not quite right. The 'rate' is a specific term for a fraction 
                            describing the relationship a thing like weight, and time. Rates can be reviewed`},
                            {text: `here at Math Antics on Youtube`, href: 'https://youtu.be/RQ2nYUBVvqI?t=342', leave:true}
                        ]},
                    ],
                    choice: {options: [
                        <Text text={"Nah, back to thinking!"}  out={[
                            {type: 'set', path: 'temp.node', value: 'start'}
                        ]} />
                    ]}}); 
            case 'ratio': 
                let ratioOptions = ['rate', 'ratio', 'proportion', 'fraction']
                return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                        convoId: 'mistake', textId: node, text: [
                            {text: `The relationship between the weight of toxic waste broken down over a period of time is called the...`}
                        ]},
                    ],
                    choice: {options: ratioOptions.map((r, i) => {
                        return <Text text={r}  out={[
                            {type: 'set', path: 'temp.node', value: i===0?'rateEq':'ratio-err'},
                            MCQ(keys.acMath, keys.qProportionRatio, i + "", i===0?100:0, [
                                {text: `When what the term for the relationship between the weight and time of the sample work was, 
                                player ${i===0?'':'in'}correctly answered ${r}, from the options ${ratioOptions}`}
                            ])
                        ]} />
                    })}});  
            default: return Scene(`${id}-start`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: 'start', text: [
                        {text: `So I need to figure out how long it'll take the fairies to break down all
                        the toxic waste on the job site`}
                    ]},
                ],
                choice: {options: [
                <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'ratio'}} />
            ]}});
        }
    }
}
//dgGramsToKilogramsMistake
export const dgGramsToKilogramsMistake = {
    key: 'dgGramsToKilogramsMistake',
    pull: (read, write, request) => {
        let {sampleSize, siteSize} = request;
        let unitOptions = ['weight', 'time', 'length', 'speed'];
        unitOptions = unitOptions.map((e, i) => {
            return {text: e, out: [
                {type: 'set', path: 'temp.node', value: i===0?'metric':'unitErr'},
                MCQ(keys.acMath, keys.qGtoKgUnit, e, i===0?100:0, i === 0?null:[
                    {text: `When asked what grams and kilograms measured, the player ${i===0?'':'in'}correctly
                    answered ${e}, from the options ${listToEnglish(unitOptions)}`}
                ],  false)
            ]}
        })
        let metricOptions = ['grams', 'kilograms', 'milligrams']
        metricOptions = metricOptions.map((e, i) => {
            return {text: e,
                out: [
                    {type: 'set', path: 'temp.node', value: i===0?'relative':'metricErr'},
                    MCQ(keys.acMath, keys.qMetricBase, e, i===0?100:0, i === 0?null:[
                        {text: `When asked what the base unit for weight was, the player ${i===0?'':'in'}correctly
                        answered ${e}, from the options ${listToEnglish(metricOptions)}`}
                    ],  false)
                ]
            }
        })
   
        let gToKgOptions = [
            {eq: `grams*1/1000`, latex: getMultiple('grams', getFraction('1', '1000'))}, 
            {eq: `grams*1000`, latex: getMultiple('grams', '1000')}, 
            {eq: `grams*1/0.001`, latex: getMultiple('grams', getFraction('1', '0.001'))}, 
            {eq: `grams*0.001`, latex: getMultiple('grams', '0.001')}
        ]
        gToKgOptions = gToKgOptions.map((e, i)=>{
            return {...e, i, out: [
                {type: 'set', path: 'temp.node', value: i===0?'conversion':'relativeErr'},
                {type: 'set', path: 'temp.selectedConversionEq', value: e},
                MCQ(keys.acMath, keys.qTokg, e.eq, i===0?100:0, i === 0?null:[
                    {text: `When asked what the base unit for weight was, the player ${i===0?'':'in'}correctly
                    answered`},
                    {latex: e.latex},
                    {text: `from the options`},
                    ...gToKgOptions.map(eq => {
                        return {latex: eq.latex}
                    })
                ],  false)
            ]};
        });
        let kiloOptions = [`1000 grams`, '0 grams', '-1000 grams', '99 grams']
        kiloOptions = kiloOptions.map((e, i) => {
            return {text: e, out: [
                {type: 'set', path: 'temp.node', value: i===0?'relative':'relativeErr2'},
                MCQ(keys.acMath, keys.qGinKg, e, i===0?100:0, i === 0?null:[
                    {text: `When asked what the 'kilo' in kilograms equalled in grams, the player ${i===0?'':'in'}correctly
                    answered ${e}, from the options ${listToEnglish(kiloOptions)}`}
                ],  false)
            ]}
        });
        return {unitOptions, metricOptions, gToKgOptions, kiloOptions, ...request}
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        let unitOptions = write.temp.unitOptions;
        let metricOptions = write.temp.metricOptions;
        let gToKgOptions = write.temp.gToKgOptions;
        let kiloOptions = write.temp.kiloOptions;
        let selectedConversionEq = write.temp.selectedConversionEq;
        let sampleSize = write.temp.sampleSize;
        switch(node){
            case 'conversion': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `So converting the sample of ${sampleSize.value} ${sampleSize.unit} with the operation`},
                        {latex: selectedConversionEq.latex},
                        {text: `should give me the right answer`}
                    ]},
                ],
                choice: {options: [<Text text={'next'}  out={[
                    {end: true}
                ]} />]}});
            case 'relativeErr2': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `No kilogram means 1000 grams. Review the weight units`},
                        {text: 'here with khan academy on youtube', href: 'https://www.youtube.com/watch?v=TD1zuENbEdk', leave: true}
                    ]},
                ],
                choice: {
                    options: [<Text text="next" out={[{type: 'set', path:'temp.node', value: 'relative'}]} />]
                }});
            case 'relativeErr': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `Actually, kilo stems from a Greek word meaning 1000... so kilograms should mean...?`}
                    ]},
                ],
                choice: {
                    options: kiloOptions.map(e => {
                        return <Text {...e} />
                    })
                }});
            case 'relative': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `The 'kilo' part of kilogram means it's a multiple of gram somehow... 
                        which operation would convert grams to kilograms?`}
                    ]},
                ],
                choice: {
                    options: gToKgOptions.map(e => {
                    return <Text {...e} />
                })}});
            case 'metricErr': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: node,text: [
                        {text: `The fact that kilogram and milligram from contain the word 'gram', 
                        probably means they aren't the base unit for weight, they're multiple of it.`}
                    ]},
                ],
                choice: {options: [<Text text={"next"}  out={[
                    {type: 'set', path:'temp.node', value: 'metric'}
                ]} />]}});
            case 'metric': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `In the metric system, what is the base unit that describes weight?`}
                    ]},
                ],
                choice: {options: metricOptions.map(e => {
                    return <Text text={e.text}  out={e.out} />
                })}});
            case 'unitErr': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `Wait, that's not right. Grams and Kilograms actually describe the weight of something.`}
                    ]},
                ],
                choice: {options: [
                <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'start'}} />
            ]}});
            default: return Scene(`${id}-start`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                        convoId: 'mistake', textId: 'start', text: [
                            {text: `First off, grams and kilograms both describe...?`}
                        ]},
                    ],
                    choice: {options: unitOptions.map(e => {
                        return <Text text={e.text}  out={e.out} />
                    })}});
            }
    }
}

export const dgWorkRateNeg =  {
        key: 'dgWorkRateNeg',
        pull: (read, write, mistake) => {
            let workRate = mistake.eq;
            let totalTime = 'totalTime';
            let xHoursAgo = {eq: `1/${totalTime} = 1/${workRate}`, latex: `${getFraction(1, totalTime)} = ${getFraction(1, workRate)}`};
            let hoursAgoOptions = [workRate, workRate*10, workRate*-10, workRate*0.1];
            return {
                xHoursAgo, workRate, //otherWorkRate, xCombined, lcms, 
                hoursAgoOptions, totalTime
            }
        },
        dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
            let workRate = get('temp.workRate', read, write, null); 
            let xHoursAgo = get('temp.xHoursAgo', read, write, null); 
            let hoursAgoOptions = get('temp.hoursAgoOptions', read, write, null); 
            let totalTime = get('temp.totalTime', read, write, null); 
            let operatedEq = get('temp.operatedEq', read, write, null); 
            let crossMultiplyOptions = get('temp.crossMultiplyOptions', read, write, null); 
            let crossMultipliedAnswer = get('temp.crossMultipliedAnswer', read, write, null); 
            let doneXHoursAgoAnswer = get('temp.doneXHoursAgoAnswer', read, write, null); 
            switch(node){
                case 'combinedWork': return Scene('combinedWork-start', {settings, viewProps, makeChange,
                        img: [
                            {id:  keys.nMc,
                            imageId: keys.getImg('nMc'), pose: 'neutral',
                            convoId: 'mistake', textId: 'tdcw', text: [
                                {text: `Right, so the equation must be wrong, as it takes time to finish a job. Their work rate must be positive`},
                            ], initial: ['pL'], animate: ['aL']},
                        ],
                        choice: {
                            options: [
                                <Text text={"next"}  out={{end: true}} />
                            ]
                        }
                    });
                case 'crossMultipliedAnswer': 
                    if(crossMultipliedAnswer === workRate){
                        return Scene('crossMultipliedAnswerright-start', {settings, viewProps, makeChange,
                            img: [
                                {id:  keys.nMc,
                                imageId: keys.getImg('nMc'), pose: 'neutral',
                                convoId: 'mistake', textId: 'tdcma2', text: [
                                    {text: `Right, this equation makes total time equal ${crossMultipliedAnswer}. 
                                    Since a negative work rate is impossible, 
                                    I've somehow built the wrong equation with my previous steps.`},
                                ], initial: ['pL'], animate: ['aL']},
                            ],
                            choice: {
                                options: [
                                    <Text text={'next'}  out={[
                                        {end:true},
                                    ]} />
                                ]
                            }
                        }); 
                    }else{
                        return Scene('crossMultipliedAnswerwrong-start', {settings, viewProps, makeChange,
                            img: [
                                {id:  keys.nMc,
                                imageId: keys.getImg('nMc'), pose: 'neutral',
                                convoId: 'mistake', textId: 'tdcma', text: [
                                    {text: `No, the equation`},
                                    {latex: operatedEq.latex},
                                    {text: `says: "The totalTime is ${workRate} hours", not ${crossMultipliedAnswer} hours.`},
                                    {text: `Either way, the equation doesn't make sense, I must have made a mistake somewhere`},
                                ], initial: ['pL'], animate: ['aL']},
                            ],
                            choice: {
                                options: [
                                    <Text text={'next'} out={[
                                        {type: 'set', path: 'temp.node', value: 'start'},
                                    ]} />
                                ]
                            }
                        }); 
                    }
                case 'crossMultiplied': return Scene('crossMultiplied-start', {settings, viewProps, makeChange,
                    img: [
                        {id:  keys.nMc,
                        imageId: keys.getImg('nMc'), pose: 'neutral',
                        convoId: 'mistake', textId: 'tdcm', text: [
                            {text: `Which gives me`},
                            {latex: operatedEq.latex},
                            {text: `, cross multiplying works because it makes totalTime the subject.`},
                            {text: `So, according to this equation, this employee would finish the job in...`}
                        ], initial: ['pL'], animate: ['aL']},
                    ],
                    choice: {
                        options: crossMultiplyOptions.map((e, i) => {
                            return <Text text={`${e} hours`} i={i}  out={[
                                {type: 'set', path: 'temp.node', value: 'crossMultipliedAnswer'},
                                {type: 'set', path: 'temp.crossMultipliedAnswer', value: e},
                                MCQ(keys.acMath, keys.qMistNegEqTTValue, null, i===0?100:0,i===0?null:[
                                    `From the equation`,
                                    {latex: operatedEq.latex},
                                    {text: `player answered that totalTime equalled ${e} hours, 
                                    when the answer was ${crossMultiplyOptions[0]} hours`}
                                ])
                            ]} />
                        })
                    }
                }); 
                case 'squared':  return Scene('squared-start', {settings, viewProps, makeChange,
                    img: [
                        {id:  keys.nMc,
                        imageId: keys.getImg('nMc'), pose: 'neutral',
                        convoId: 'mistake', textId: 'tdsq', text: [
                            {text: `Which gives me`},
                            {latex: operatedEq.latex},
                            {text: `, which is not right, as I need totalTime to be the subject`}
                        ], initial: ['pL'], animate: ['aL']},
                    ],
                    choice: {
                        options: [<Text text={"back"} out={{type: 'set', path: 'temp.node', value: 'fractionTime'}} />],
                    }
                }); 
                case 'fractionTime1': return Scene('fractionTime1-start', {settings, viewProps, makeChange,
                        img: [
                            {id:  keys.nMc,
                            imageId: keys.getImg('nMc'), pose: 'neutral',
                            convoId: 'mistake', textId: 'tdft1', text: [
                                {text: `I need to do some extra operations to make totalTime the subject of the equation`},
                                {latex: xHoursAgo.latex},
                                {text: `. Which operation will make totalTime the subject of the equation?`}
                            ], initial: ['pL'], animate: ['aL']},
                        ],
                        choice: {
                            onInput: (id, v)=>{
                                //xHoursAgo.eq
                                let node='', result, crossMultiplyOptions, operation;
                                if(v.i === 0){
                                    result =doOperation([xHoursAgo.eq], MULT, [`${workRate}*${totalTime}`], 1);
                                    result = result[0][0];
                                    result = {
                                        eq: `${getEqSide(result, 1)}=${getEqSide(result, 0)}`,
                                        latex: toLatex(`${getEqSide(result, 1)}=${getEqSide(result, 0)}`)
                                    }
                                    let num = toNum(getEqSide(result.eq, 1));
                                    crossMultiplyOptions = [num, num*10, num*-10, num*0.1]
                                    node = 'crossMultiplied';
                                }else if(v.i === 1){
                                    result = doOperation([xHoursAgo.eq], POW, [2], 1);
                                    result = result[0][0];
                                    node = 'squared';
                                }else if(v.i === 2){
                                    result = doOperation([xHoursAgo.eq], MULT, ['totalTime'], 1);
                                    result = result[0][0];
                                    node = 'squared';
                                }else if(v.i === 3){
                                    result = doOperation([xHoursAgo.eq], MULT, [workRate], 1);
                                    result = result[0][0];
                                    node = 'squared';
                                }
                                makeChange(id, [
                                    {type: 'set', path: 'temp.node', value: node},
                                    {type: 'set', path: 'temp.operatedEq', value: result},
                                    {type: 'set', path: 'temp.crossMultiplyOptions', value: crossMultiplyOptions},
                                    MCQ(keys.acMath, keys.qMistNegEqTTOperation, node, v.i===0?100:0, v.i === 0?null:[
                                        {text: `When asked to select an operation that would make totalTime the subject of the equation`},
                                        {latex: xHoursAgo.latex},
                                        {text: `the player selected '${v.text}', when the answer was 'cross multiply'`}
                                    ], true)
                                ])
                            },
                            options: [
                                <Text text={"cross multiply"} i={0}  />,
                                <Text text={"square it"} i={1}   />,
                                <Text text={"multiply by totalTime"} i={2}   />,
                                <Text text={"multiply by " + workRate} i={3}   />,
                            ]
                        }
                    }); 
                    //doneXHoursAgoAnswer
                case 'fractionTime': return Scene('fractionTime-start', {settings, viewProps, makeChange,
                    img: [
                        {id:  keys.nMc,
                        imageId: keys.getImg('nMc'), pose: 'neutral',
                        convoId: 'mistake', textId: 'tdft', text: [
                            {text: `${doneXHoursAgoAnswer} hours doesn't sound right...`},
                        ], initial: ['pL'], animate: ['aL']},
                    ],
                    choice: {options: [
                        <Text text={`next`}  out={[
                            {type: 'set', path: 'temp.node', value: 'fractionTime1'},
                        ]} />
                    ]
                }}); 
                case 'doneXHoursAgo2':
                    return Scene('doneXHoursAgo2-start', {settings, viewProps, makeChange,
                        img: [
                            {id:  keys.nMc,
                            imageId: keys.getImg('nMc'), pose: 'neutral',
                            convoId: 'mistake', textId: 'tdxha2', text: [
                                {text: `If they were working alone, and their work rate was ${workRate} then from the equation`},
                                {latex: xHoursAgo.latex},
                                {text: `the total time the job takes would be...`}
                            ], initial: ['pL'], animate: ['aL']},
                        ],
                        choice: {options: hoursAgoOptions.map((e, i) => {
                            let next = null;
                            if(i === 0){
                                next = 'combinedWork';
                            }else{
                                next = 'fractionTime';
                            }
                            return <Text text={`${e} hours`}  out={[
                                {type: 'set', path: 'temp.doneXHoursAgoAnswer', value: e},
                                {type: 'set', path: 'temp.node', value: next},
                                MCQ(keys.acMath, keys.qMistNegEqTTDirect, e > 0?'pos':'neg', i===0?100:0, i===0?null:[
                                    `answered that the totalTime in`,
                                    {latex: xHoursAgo.latex}, 
                                    {text: `was equal to ${e} hours, when it was really equal to ${workRate} hours`}
                                ])
                            ]} />
                        })
                    }}); 
                case 'doneXHoursAgo':
                    return Scene('doneXHoursAgo-start', {settings, viewProps, makeChange,
                        img: [
                            {id:  keys.nMc,
                            imageId: keys.getImg('nMc'), pose: 'neutral',
                            convoId: 'mistake', textId: 'tdxha', text: [
                                {text: `Yes. It makes no sense for a job to take negative time, 
                                so a negative work rate is impossible.`},
                            ], initial: ['pL'], animate: ['aL']},
                        ],
                        choice: {options: [
                            <Text text={`next`}  out={[
                                {type: 'set', path: 'temp.node', value: 'doneXHoursAgo2'},
                            ]} />
                        ]
                    }}); 
                case 'negativeDinner2': 
                let negativeDinnerAnswer = get('temp.negativeDinnerAnswer', read, write, null);
                return Scene('negativeDinner2-start', {settings, viewProps, makeChange,
                    img: [
                        {id:  keys.nMc,
                        imageId: keys.getImg('nMc'), pose: 'doubt',
                        convoId: 'mistake', textId: 'tdnd2', text: [
                            {text: `${negativeDinnerAnswer}? Maybe...but no, it doesn't make much sense. There's no such thing as negative time. 
                            I've made a mistake if I'm getting a negative answer for time.`},
                        ], initial: ['pL'], animate: ['aL']},
                    ],
                    choice: {options: [
                        <Text text={"nothing"}  out={{type: 'set', path: 'temp.node', value: 'start'}} />,
                    ]}}); 
                case 'negativeDinner': 
                    let negativeDinnerOptions = ['nothing', 'sleep', 'studying'];
                        return Scene('negativeDinner-start', {settings, viewProps, makeChange,
                        img: [
                            {id:  keys.nMc,
                            imageId: keys.getImg('nMc'), pose: 'doubt',
                            convoId: 'mistake', textId: 'tdnd1', text: [
                                {text: `Time is represented by a positive number. A negative number would mean the job took negative time...
                                        What the heck gives you back time?`},
                            ], initial: ['pL'], animate: ['aL']},
                        ],
                        choice: {options: negativeDinnerOptions.map(nd => {
                            return <Text text={nd}  out={[
                                {type: 'set', path: 'temp.node', value: 'negativeDinner2'},
                                {type: 'set', path: 'temp.negativeDinnerAnswer', value: nd}
                            ]} />
                        })}}); 
                case 'firstReason': return Scene('firstReason-start', {settings, viewProps, makeChange,
                    img: [
                        {id:  keys.nMc,
                        imageId: keys.getImg('nMc'), pose: 'neutral',
                        convoId: 'mistake', textId: 'tdfr1', text: [
                            {text: `If the work rate `, className: 'inline'},
                            {text: 'x', color: 'primary', className: 'inline'},
                            {text: `measures how many hours it will take this employee to do one job, 
                            a negative work rate would mean...`, className: 'inline'}
                        ], animate: ['aL']},
                    ],
                    choice: {options: [
                    <Text text={"The job takes negative time to complete."}  
                    out={[
                        {type: 'set', path: 'temp.node', value: 'doneXHoursAgo'},
                        MCQ(keys.acMath, keys.qMistNegTimeSemantics, 'past', 100)
                    ]} />,
                    <Text text={"The job takes time to complete."}  out={[
                        {type: 'set', path: 'temp.node', value: 'negativeDinner'},
                        MCQ(keys.acMath, keys.qMistNegTimeSemantics, 'future', 0, [`Mistinterpreted a negative work rate as meaning 
                        'The job takes time to complete' 
                        when it actually means 'The job takes negative time to complete'`])
                    ]} />
                ]}});
                default: return Scene('dgWorkRateNeg-start', {settings, viewProps, makeChange,
                    audio: [
                        {id: "f-annoyed"}
                    ],
                    img: [
                        {id:  keys.nMc,
                        imageId: keys.getImg('nMc'), pose: 'shocked',
                        convoId: 'mistake', textId: 'tdef1', text: [
                            {text: `Since I'm looking for this person's work rate, a negative answer doesn't to make much sense.`},
                        ], initial: ['pL'], animate: ['aL']},
                    ],
                    choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'firstReason'}} />
                ]}});
            }
        }
}


export const dgNoVar = Dialogue(
    'dgNoVar',
    (read, write, mistake) => {
        let eq = mistake.eq;
  
        let lhs = getEqSide(eq.eq, 0);
        let rhs = getEqSide(eq.eq, 1);
        return {eq, lhs: roundTo(evaluate(lhs), 2), rhs: roundTo(evaluate(rhs), 2)}
    },
    (id, node, read, write, makeChange, settings, viewProps) => {
        switch(node){
            case 'need': return Scene('dgNoVar-need', {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgNoVar', textId: 'need', text: [
                        {text: `I need an equation with a variable to represent the unknown I'm trying to solve for`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{end:true}} />
                ]
            }});
            case 'evaluated': return Scene('dgNoVar-evaluated', {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgNoVar', textId: 'evaluated', text: [
                        {text: `This equation evaluates to ${write.temp.lhs}=${write.temp.rhs}.`},
                        write.temp.lhs!==write.temp.rhs?{text: `Which is definitely wrong.`}:null
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'need'}} />
                ]
            }});
            case 'noVar': return Scene('dgNoVar-noVar', {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgNoVar', textId: 'noVar', text: [
                        {text: `If my equation has no variables, then there's no unknown answer that I can solve for.`},
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'evaluated'}} />
                ]
            }});
            default: return Scene('dgNoVar-start', {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgNoVar', textId: 'start', text: [
                        {text: `Wait, the point of variables is to represent the unknown. The equation`},
                        {latex: write.temp.eq.latex},
                        {text: `has no variables.`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'noVar'}} />
                ]
            }});
        }
    }
)

export const dgTwoVars = Dialogue(
    'dgTwoVars',
    (read, write, mistake) => {
        let demoEq = toLatex(`5=a+b`);
        return {eq: mistake.eq, variables: getVariables(mistake.eq.eq), demoEq}
    },
    (id, node, read, write, makeChange, settings, viewProps) => {
        switch(node){
            case 'needOne': return Scene(`dgTwoVars-${node}`, {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgTwoVars', textId: node, text: [
                        {text: `To find an unknown I can only have one variable in the equation`},
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{end: true}} />
                ]
            }});
            case 'demo-right': return Scene(`dgTwoVars-${node}`, {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgTwoVars', textId: node, text: [
                        {text: `Exactly. I can't know because it could be any answer. Any value of a 
                            has a partner b that can make it equal 5.`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'needOne'}} />,
                ]
            }});
            case 'demo-wrong-3': return Scene(`dgTwoVars-${node}`, {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgTwoVars', textId: node, text: [
                        {text: `When solving an equation, I want a specific value. 
                        Since I can't figure out a specific value for a or b in the equation`},
                        {latex: write.temp.demoEq},
                        {text: `. I can't solve it.`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'demo'}} />,
                ]
            }});
            case 'demo-wrong-2': return Scene(`dgTwoVars-${node}`, {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgTwoVars', textId: node, text: [
                        {text: `Making`},
                        {latex: write.temp.demoEq},
                        {text: `true for all values.`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'demo-wrong-3'}} />,
                ]
            }});
            case 'demo-wrong': return Scene(`dgTwoVars-${node}`, {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgTwoVars', textId: node, text: [
                        {text: `It could be ${write.temp.demoAnswer}, but it could also be any other, like 10, or 8, or 1000 as well. 
                        Every possible value for a, has a value for b that would sum to 5.`},
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'demo-wrong-2'}} />,
                ]
            }});
            case 'demo': return Scene(`dgTwoVars-${node}`, {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgTwoVars', textId: node, text: [
                        {text: `So in the equation`},
                        {latex: write.temp.demoEq},
                        {text: `What's the value of a?`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"I can't know"}  out={[
                        {type: 'set', path: 'temp.node', value: 'demo-right'}
                    ]} />,
                    <Text text={"500"}  out={[
                        {type: 'set', path: 'temp.node', value: 'demo-wrong'},
                        {type: 'set', path: 'temp.demoAnswer', value: '500'}
                    ]} />,
                    <Text text={"-3"}  out={[
                        {type: 'set', path: 'temp.node', value: 'demo-wrong'},
                        {type: 'set', path: 'temp.demoAnswer', value: '-3'}
                    ]} />,
                    <Text text={"0"}  out={[
                        {type: 'set', path: 'temp.node', value: 'demo-wrong'},
                        {type: 'set', path: 'temp.demoAnswer', value: '0'}
                    ]} />,
                ]
            }});
            case 'demo-eq': return Scene(`dgTwoVars-${node}`, {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgTwoVars', textId: node, text: [
                        {text: `What's a simple example...ah! here we go.`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'demo'}} />
                ]
            }});
            case 'infinite': return Scene(`dgTwoVars-${node}`, {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgTwoVars', textId: node, text: [
                        {text: `If there were two unknowns, like ${listToEnglish(write.temp.variables)}, 
                            there would be an infinite number of answers that make this equation true.`},
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'demo-eq'}} />
                ]
            }});
            default: return Scene(`dgTwoVars-start`, {settings, viewProps, makeChange,
                img: [
                    {id:  keys.nMc, imageId: keys['sisMc-img'], pose: 'neutral', initial: ['pL'], animate: ['aL'],
                    convoId: 'dgTwoVars', textId: 'start', text: [
                        {text: `Wait, this equation`},
                        {latex: write.temp.eq},
                        {text: `has two variables, ${listToEnglish(write.temp.variables)}.`}
                    ]},
                ],
                choice: {options: [
                    <Text text={"next"}  out={{type: 'set', path: 'temp.node', value: 'infinite'}} />
                ]
            }});
        }
    }
)

