export const dgSecondsToHoursMistake = Dialogue(
    'dgSecondsToHoursMistake',
    (read, write, mistake) => {
     //   let wrongAnswer = evaluate(`${mistake.wrongEq.eq}*${mistake.estimatedSeconds}`);
        let timeOptions = [60, 120, 80, 20];
        let answerHours = evaluate(`${mistake.estimatedSeconds}*(1/3600)`);
        
        // {type: 'set', path: 'temp.mistake', value: {
        //     key: 'dgSecondsToHoursMistake',
        //     solutionHours: evaluated, 
        //     estimatedSeconds,
        //     correctEq: secondsToHoursEqs[0], 
        //     wrongEq: {eq: v.eq, latex: v.latex}, request}},
        return {request: mistake.request, ...mistake, //wrongAnswer: roundTo(wrongAnswer, 2), 
            timeOptions, answerHours}
    },
    (id, node, read, write, makeChange, settings, viewProps) => {
        console.log(`dialogue dgSecondsToHoursMistake node ${node} write`, write);
        let wrongEq = write.temp.wrongEq;
        let estimatedSeconds = write.temp.estimatedSeconds;
      //  let wrongAnswer = write.temp.wrongAnswer;
        let timeOptions = write.temp.timeOptions;
        let secondsInMinute = write.temp.secondsInMinute;
        let minutesToHours = write.temp.minutesToHours;
        let minutes = write.temp.minutes;
        let smhEqs = write.temp.smhEqs;
        let answerHours = write.temp.answerHours;
        let userAnswer = write.temp.userAnswer;
        let isCorrect = write.temp.isCorrect;
        let secToMinEq = write.temp.secToMinEq;
        let operation = write.temp.operation;
        switch(node){
            case 'end': return Scene(`dgSecondsToHoursMistake-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        isCorrect?{text: `Yes ${roundTo(userAnswer, 2)} hours seems right!`}
                            :{text:`${roundTo(userAnswer, 2)} hours seems a little off...`}
                    ]},
                ],
                choice: {
                    options: [
                        <Text text="next" out={[
                            {end: true}
                        ]}  />
                    ]
            }});
            case 'minToHourEqs': return Scene(`dgSecondsToHoursMistake-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `If there are ${secondsInMinute} seconds in a minute, and ${minutesToHours} minutes
                        in an hour, then to convert ${roundTo(estimatedSeconds, 2)} seconds to hours I'll need the equation...`},
                    ]},
                ],
                choice: {
                    onInput: (id, v) => {
                        let ans = evaluate(`${v.eq}*${estimatedSeconds}`);
                        makeChange(id, [
                            MCQ('secToHour', 'secToHourEq', null, v.i===0, [
                                {text: `Player ${v.i===0?`correctly`:`incorrectly`} thought the equation to convert seconds to hours was`},
                                {latex: v.latex},
                                v.i === 0?null:{text: `when the actual equation was`},
                                v.i === 0?null:{latex: smhEqs[0].latex}
                            ]),
                            {type:'set', path: 'temp.node', value: 'end'},
                            {type:'set', path: 'temp.userAnswer', value: ans},
                            {type:'set', path: 'temp.isCorrect', value: roundTo(ans, 2) === roundTo(answerHours, 2)},
                        ])
                    },
                    options: smhEqs.map((e, i) => {
                        return <Text {...e} i={i} />
                    })
            }});
            case 'minToHour': return Scene(`dgSecondsToHoursMistake-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {latex: secToMinEq.latex},
                        {text: `multiplied by ${roundTo(estimatedSeconds, 2)} would equal ${roundTo(minutes, 2)} minutes. 
                        The client wants it in hours... How many minutes are there in an hour?`},
                    ]},
                ],
                choice: {
                    onInput: (id, v) => {
                        let stm = secondsInMinute;
                        let mth = v.text;
                        let solution = evaluate(`${stm}*${mth}`);
                        let multiples = [1, 10];
                        let solutions = [];
                        multiples.map(e => {
                            solutions.push({eq: `${estimatedSeconds}*1/${e*solution}`, 
                                            latex: getMultiple(roundTo(estimatedSeconds, 2), getFraction(1, e*solution))});
                            solutions.push({eq: `${estimatedSeconds}*${e*solution}`, 
                                            latex: getMultiple(roundTo(estimatedSeconds, 2), roundTo(e*solution, 2))});
                        })
                        makeChange(id, [
                            MCQ('secToHour', 'minInHour', null, v.i===0, [
                                {text: `Player ${v.i===0?`correctly`:`incorrectly`} thought there were ${v.text} minutes in an hour`},
                            ]),
                            {type:'set', path: 'temp.node', value: 'minToHourEqs'},
                            {type:'set', path: 'temp.minutesToHours', value: mth},
                            {type:'set', path: 'temp.smhEqs', value: solutions},
                        ])
                    },
                    options: timeOptions.map((t, i) => {
                        return <Text text={t} i={i} />
                    })
            }});
            case 'operation':
                let secToMinEqs = [
                    {eq: `${estimatedSeconds}/${secondsInMinute}`, latex: getFraction(roundTo(estimatedSeconds, 2), secondsInMinute)},
                    {eq: `${estimatedSeconds}*${secondsInMinute}`, latex: getMultiple(roundTo(estimatedSeconds, 2), secondsInMinute)},
                ]
                return Scene(`dgSecondsToHoursMistake-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                        convoId: 'mistake', textId: node, text: [
                            {text: `Since I think there are ${secondsInMinute} seconds in a minute,  
                            to convert ${roundTo(estimatedSeconds, 2)} seconds to minutes I should use`}
                        ]},
                    ],
                    choice: {
                        onInput: (id, v)=>{
                            let solution = evaluate(v.eq);
                            makeChange(id, [
                                v.i === 0?MCQ('secToHour', 'secInMinOperation', null, v.i===0, [
                                    {text: `Player correctly they needed to divide the number of seconds to convert to minutes`},
                                ]):MCQ('secToHour', 'secInMinOperation', null, v.i===0, [
                                    {text: `Player incorrectly they needed to multiply the number of seconds to convert to minutes`},
                                ]),
                                {type:'set', path: 'temp.node', value: 'minToHour'},
                                {type:'set', path: 'temp.minutes', value: solution},
                                {type:'set', path: 'temp.secToMinEq', value: {eq: v.eq, latex: v.latex}},
                                {type:'set', path: 'temp.operation', value: v.i === 0?'divide':'multiply'},
                            ])
                        },
                        options: secToMinEqs.map((e, i) => {
                            return <Text {...e} i={i}  />
                        })
                }});
            case 'size': return Scene(`dgSecondsToHoursMistake-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('Mc', 'Mc', 0, null, 'aL'),
                        convoId: 'mistake', textId: node, text: [
                            {text: secondsInMinute === 60?`Yes, that's right`:"mmm...that doesn't feel right but carry on."},
                            {text: `When I convert this time from seconds to minutes...`},
                        ]},
                    ],
                    choice: {options: [
                        <Text text={'I expect the number will be smaller'}  out={[
                            {type:'set', path: 'temp.node', value: 'operation'},
                            MCQ('secToHour', 'secInMinSize', null, 1, [
                                {text: `Player correctly thought the number of minutes would be smaller than the number of seconds, when converted`},
                            ])
                        ]} />,
                        <Text text={'I expect the number will be larger'}  out={[
                            {type:'set', path: 'temp.node', value: 'operation'},
                            MCQ('secToHour', 'secInMinSize', null, 0, [
                                {text: `Player incorrectly thought the number of minutes would be larger than the number of seconds, when converted`},
                            ])
                        ]} />
                    ]
                }});
            default: return Scene(`dgSecondsToHoursMistake-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: node, text: [
                        {text: `Back to basics. How many seconds are there in a minute?`},
                    ]},
                ],
                choice: {options: timeOptions.map((t, i) => {
                    return <Text text={t}  out={[
                        {type:'set', path: 'temp.node', value: 'size'},
                        {type:'set', path: 'temp.secondsInMinute', value: t},
                        MCQ('secToHour', 'secInMin', null, i===0, [
                            {text: `Player ${i===0?'correctly':'incorrectly'} answered that there are ${t} seconds in a minute, 
                            when there are ${timeOptions[0]}`},
                        ])
                    ]} />
                })
            }});
            // default: return Scene('dgSecondsToHoursMistake-start', {settings, viewProps, makeChange,
            //     img: [
            //         {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
            //         convoId: 'mistake', textId: 'tdeadf', text: [
            //             {text: `Ah no if ${roundTo(estimatedSeconds, 2)} were converted to hours with`},
            //             {latex: wrongEq.latex},
            //             {text: ` that would equal ${wrongAnswer} hours, that seems a little off.`}
            //         ]},
            //     ],
            //     choice: {options: [
            //         <Text text={"next"}  out={{type:'set', path: 'temp.node', value: 'smallerLarger'}} />
            //     ]
            // }});
        }
    }
)//dgTimeRatioMistake


export const dgTimeRatioMistake = Dialogue(
    'dgTimeRatioMistake',
    (read, write, mistake) => {
        return {request: mistake.request, correctEq: mistake.correctEq, wrongEq: mistake.wrongEq}
    },
    (id, node, read, write, makeChange, settings, viewProps) => {
        switch(node){
            default: return Scene('dgTimeRatioMistake-start', {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('Mc', 'Mc', 0, 'iL', 'aL'),
                    convoId: 'mistake', textId: 'tdef1', text: [
                        {text: `Mistake dialogue dgTimeRatioMistake`},
                    ]},
                ],
                choice: {options: [
                        <Text text={"next"}  out={{end:true}} />
                    ]
            }});
        }
    }
)//dgTimeRatioMistake