export const getBusinessState = (read, write) => {
    log(`view text getRequestText read`, read);
    let money = get('values.money.value', read, write, 0);
    let reviews = get('values.reviewAggregate', read, write, 0);
    let numReviews = get(`meta.numReviews`, read, write, 0);
    let texts = [];
    texts.push({text: `Objective`, tag: 'h5', className: 'block text-center'});
    texts.push({text: `"If we don't have an average review score greater tha 4 by the time we have ${numReviews} reviews, I'm out of this business."`, className: 'block text-center'});
    texts.push({space:true});
    texts.push({text: `Reviews`, tag: 'h3', className: 'block text-center'});
    texts.push({text: `Review Count: ${reviews.total} review average: ${reviews.avg}`, className: 'block text-center'});

    return texts;
}