import React from 'react';
import {get, DELETED} from 'utilities/Merge';
import Text from 'game/Views/TextManager/TextManager';
import Scene from 'game/Views/Scene/Scene.js';
import {log} from 'utilities/Measure';
import {MCQ,Operation, GaEvent, Dialogue} from 'utilities/Create.js';
import {getEqSide, getImg, roundTo, listToEnglish, shuf} from 'utilities/Format.js';
import {operate, EQLR, expand, toLatex, solveFor, toNum, 
        least_common_multiple, getVariables} from 'utilities/Game/MathFun';
import { MULT, POW, DIV, DIFF, doOperation, getConversionFormula, evaluate, 
        interpret, substitute} from 'utilities/Game/TreeMath.js';
import {getEmployeeDescription} from './../texts.js';
import {partialEmployeeView} from './../views.js';
import {getFraction, getMultiple} from 'utilities/Game/Latex';
import {getWorkRateEqs} from './../definitions';
import keys from './../keys';

export const dgHeadSister = Dialogue(
    'dgHeadSister',
    (read, write) => {
        let reviewAggregate = get('values.reviewAggregate', read, write, {});
        return {reviewAggregate}
    },
    (id, node, read, write, makeChange, settings, viewProps) => {
        console.log(`dialogue ${id} node ${node} write`, write);
        let reviewAggregate = write.temp.reviewAggregate;
        switch(node){
            case 'good':
                return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('mc', 'Mc', 3, null, 'aL')},
                        {...keys.getSis('sisFast', 0, 1, null, 'aR'), convoId: 'mistake', textId: node, text: [
                            {text: `Excellent! Do keep those tasty jobs coming, please.`},
                        ]},
                    ],
                    choice: {options: [
                        <Text text={`next`}  out={[{end: true}]} />,
                    ]}
                });
            case 'bad':
                return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('mc', 'Mc', 3, null, 'aL')},
                        {...keys.getSis('sisFast', 0, 2, null, 'aR'), convoId: 'mistake', textId: node, text: [
                            {text: `Ah... as long as you focus on combining our work rates together and converting
                            them to hours for the clients, it should be alright?`},
                        ]},
                    ],
                    choice: {options: [
                        <Text text={`"Yeah, you're right"`}  out={[{end: true}]} />,
                        <Text text={`"Maybe..."`}  out={[{end: true}]} />,
                    ]}
                });
            default: return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 3, 'iL', 'aL')},
                    {...keys.getSis('sisFast', 0, 0, 'iR', 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `How are the reviews and those averages you're worried about going?`},
                    ]},
                ],
                choice: {options: [
                    <Text text={`"We're averaging at ${reviewAggregate.avg}, so its good!"`}  
                        out={[{type: 'set', path: 'temp.node', value: 'good'}]} />,
                        <Text text={`"We're averaging at ${reviewAggregate.avg}, so its bad."`}  
                            out={[{type: 'set', path: 'temp.node', value: 'bad'}]} />,
                ]}
            });
        }
    }
)
export const dgSister2 = Dialogue(
    'dgSister2',
    (read, write) => {
        return {}
    },
    (id, node, read, write, makeChange, settings, viewProps) => {
        console.log(`dialogue ${id} node ${node} write`, write);
        switch(node){
            case 'blooming': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 1, 'iL', 'aL')},
                    {...keys.getSis('sis2', 2, 2, 'iR', 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `Guess I'm just a late bloomer! I suppose there is something...but I'm taking a while to bloom, so to speak.`},
                    ]},
                ],
                choice: {options: [
                    <Text text={`"Keep at it"`}  out={[{end: true}]} />,
                ]}
            });
            case 'combined': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 1, 'iL', 'aL')},
                    {...keys.getSis('sis2', 2, 2, 'iR', 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `That's true... thanks ${keys.sisMc}`},
                    ]},
                ],
                choice: {options: [
                    <Text text={`"No problem"`}  out={[{end: true}]} />,
                ]}
            });
            case 'numbers': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 3, 'iL', 'aL')},
                    {...keys.getSis('sis2', 2, 1, 'iR', 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `Yes, but you're good with your math-y numbers. I'm not particularly good at magic
                        or numbers.`},
                    ]},
                ],
                choice: {options: [
                    <Text text={`"You're still making the combined work rate faster"`}  
                        out={[{type:'set', path: 'temp.node', value: 'combined'}]} />,
                    <Text text={`"Then there must be something else you're good at"`}  
                        out={[{type:'set', path: 'temp.node', value: 'blooming'}]} />,
                ]}
            });
            case 'trying': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 1, 'iL', 'aL')},
                    {...keys.getSis('sis2', 2, 2, 'iR', 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `You might be right... but I'll keep trying. I hear ${keys.sis0} was a late bloomer too.`},
                    ]},
                ],
                choice: {options: [
                    <Text text={`"Yes, she was"`}  out={[{end: true}]} />,
                ]}
            });
            case 'enjoy': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 1, 'iL', 'aL')},
                    {...keys.getSis('sis2', 2, 1, 'iR', 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `I'm enjoying them, even if I'm not quite as fast as the others at breaking the waste down.`},
                    ]},
                ],
                choice: {options: [
                    <Text text={`"You're better at it than me, at least"`}  
                        out={[{type: 'set', path: 'temp.node', value: 'numbers'}]} />,
                    <Text text={`"Yeah, there are jobs some of us just aren't cut out for"`}  
                        out={[{type: 'set', path: 'temp.node', value: 'trying'}]} />,
                ]}
            });
            default: return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 0, 'iL', 'aL'), convoId: 'mistake', textId: node, text: [
                        {text: `Hello ${keys.sis2}, how are you going with the toxic waste jobs?`},
                    ]},
                    {...keys.getSis('sis2', 2, 0, 'iR', 'aR')},
                ],
                choice: {options: [
                    <Text text={`next`}  out={[{type: 'set', path: 'temp.node', value: 'enjoy'}]} />,
                ]}
            });
        }
    }
)
export const dgFastSisterDenominator = {
    key: `dgFastSisterDenominator`,
    move: 'hub',
    pull: (read, write) => {
        let demoEq = `1/x = 1/6+1/8`
        return {
            demoEq: {eq: demoEq, latex: `${getFraction(1, 'x')}=${getFraction(1, 6)}+${getFraction(1,8)}`},
            numberEq: {eq: `1/x=${1/6}+${1/8}`, latex: `${getFraction(1, 'x')}=${roundTo(1/6, 3)}+${roundTo(1/8, 3)}`}
        };
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        let demoEq = write.temp.demoEq;
        let numberEq = write.temp.numberEq;
        switch(node){
            case 'getsIt': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                audio: [
                    {id: "f1-gasp"}
                ],
                img: [
                    {...keys.getSis('mc', 'Mc', 3, null, 'pL')},
                    {...keys.getSis('sisFast', 1, 1, null, 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `Oh I get it! So my smaller work time gives me a smaller denominator, therefore
                        a larger fraction and therefore a larger amount of work done per second?`},
                    ]},
                ],
                choice: {options: [<Text text={`Exactly`}  out={[{end: true}]} />]}
            });
            case 'numberError': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 1, 'iL', 'aL'), convoId: 'mistake', textId: node, text: [
                        {text: `The 1 represents a job. That job might involve breaking down kilos, but 
                        the equation doesnt say anything about kilos, it just says how long it takes you to do one job, whatever that is`},
                    ]},
                    {...keys.getSis('sisFast', 1, 0, null, 'pR')},
                ],
                choice: {options: [<Text text={"next"}  out={[{type: 'set', path: 'temp.node', value: 'number'}]} />]}
            });
            case 'number': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 0, 'iL', 'aL'), convoId: 'mistake', textId: node, text: [
                        {text: `If I replace the fraction with its numeric value we get`},
                        {latex: numberEq.latex},
                        {text: 'which tells us...'}
                    ]},
                    {...keys.getSis('sisFast', 1, 0, null, 'pR')},
                ],
                choice: {options: [
                <Text text={"the amount of work each of you can do per second"}  
                    out={[
                        {type: 'set', path: 'temp.node', value: 'getsIt'},
                        MCQ(keys.acGameMCQ, keys.qSFCoefficientSemantics, 'amountWork', 1)
                    ]} />,
                <Text text={"the total number of kilos you can break down"}  
                    out={[
                        {type: 'set', path: 'temp.node', value: 'numberError'},
                        MCQ(keys.acGameMCQ, keys.qSFCoefficientSemantics, 'totalKilos', 0, [
                            {text: `Player misunderstood the meaning of the coefficients in`},
                            {latex: numberEq.latex},
                            {text: `to mean "the total number of kilos you can break down" when they actually ment
                            "the amount of work each of you can do per second"
                            `}
                        ])
                    ]} />,
            ]}});
            case 'fractionError': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 1, 'iL', 'aL'), convoId: 'mistake', textId: node, text: [
                        {text: `Wait, the larger the fractions denominator is, the smaller the fraction is. 
                                So...which fraction would be larger?`},
                    ]},
                    {...keys.getSis('sisFast', 1, 0, null, 'pR')},
                ],
                choice: {options: [
                <Text text={"1/10"}  
                    out={[
                        {type: 'set', path: 'temp.node', value: 'help2'},
                        MCQ(keys.acGameMCQ, keys.qSFLargerFraction, '1/10', 1)
                    ]} />,
                <Text text={"1/100"}  
                    out={[
                        {type: 'set', path: 'temp.node', value: 'help2'},
                        MCQ(keys.acGameMCQ, keys.qSFLargerFraction, '1/100', 0, [
                            {text: `Player incorrectly answered that the fraction 1/100 was larger than 1/10`}
                        ])
                    ]} />,
            ]}});
            case 'fraction3': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 0, 'iL', 'aL'), convoId: 'mistake', textId: node, text: [
                        {text: `A fraction represents the seconds it takes one of you to break down 1 sample. 
                        Because the work time is the denominator...`},
                    ]},
                    {...keys.getSis('sisFast', 1, 0, null, 'pR')},
                ],
                choice: {options: [
                <Text text={`a larger work time produces a smaller fraction`}  
                    out={[
                        {type: 'set', path: 'temp.node', value: 'number'},
                        MCQ(keys.acGameMCQ, keys.qSFDenominatorMeaning, 'largeDenSmallFrac', 1)
                    ]} />,
                <Text text={`a larger work time produces a larger fraction`}  
                    out={[
                        {type: 'set', path: 'temp.node', value: 'fractionError'},
                        MCQ(keys.acGameMCQ, keys.qSFDenominatorMeaning, 'largeDenLargeFrac', 0, [
                            {text: `Player incorrectly answered that a large denominator produces a larger fraction value`}
                        ])
                    ]} />,
            ]}});
            case 'fraction2': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 0, 'iL', 'aL'), convoId: 'mistake', textId: node, text: [
                        {text: `Eh...we'll continue with your example`},
                        {latex: demoEq.latex}
                    ]},
                    {...keys.getSis('sisFast', 1, 0, null, 'pR')},
                ],
                choice: {options: [<Text text={"next"}  out={[{type: 'set', path: 'temp.node', value: 'fraction3'}]} />]}
            });
            case 'fraction1': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 0, 'iL', 'aL'), convoId: 'mistake', textId: node, text: [
                        {text: `You'd expect a longer work rate to be the bigger number, 
                        but because its the denominator of a fraction here, the opposite is true`},
                    ]},
                    {...keys.getSis('sisFast', 1, 0, null, 'pR')},
                ],
                choice: {options: [<Text text={"next"}  out={[{type: 'set', path: 'temp.node', value: 'fraction2'}]} />]}
            });
            case 'fraction': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                audio: [
                    {id: "f1-laugh"}
                ],
                img: [
                    {...keys.getSis('mc', 'Mc', 0, 'iL', 'aL'), convoId: 'mistake', textId: node, text: [
                        {text: `Ah I see what you're confused about`},
                    ]},
                    {...keys.getSis('sisFast', 1, 0, null, 'pR')},
                ],
                choice: {options: [<Text text={"next"}  out={[{type: 'set', path: 'temp.node', value: 'fraction1'}]} />]}
            });
            case 'help2': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                audio: [
                    {id: "f1-annoyed"}
                ],
                img: [
                    {...keys.getSis('mc', 'Mc', 0, 'iL', 'pL')},
                    {...keys.getSis('sisFast', 1, 3, null, 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `Doesnt that mean she is adding less to our combined work rate, and is therefore 
                        somehow working faster than me?`},
                    ]},
                ],
                choice: {options: [<Text text={"next"}  out={[{type: 'set', path: 'temp.node', value: 'fraction'}]} />]}});
            case 'help1': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 0, 'iL', 'pL')},
                    {...keys.getSis('sisFast', 1, 0, null, 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `So, if the denominator is our work time, so someone who takes longer to finish, like 
                        ${keys[`sis1`]}, has a bigger denominator, that makes the fraction value smaller right?`},
                    ]},
                ],
                choice: {options: [<Text text={`next`}  out={[{type: 'set', path: 'temp.node', value: 'help2'}]} />]}});
            case 'helpLie2': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                    img: [
                        {...keys.getSis('mc', 'Mc', 0, null, 'pL')},
                        {...keys.getSis('sisFast', 1, 3, null, 'aR'), convoId: 'mistake', textId: node, text: [
                            {text: `Oh good, I was worried about my memory for a bit there.`},
                        ]},
                    ],
                    choice: {options: [
                            <Text text={`next`}  out={[
                                    {type: 'set', path: 'temp.node', value: 'help1'}
                            ]} />,
                        ]}
                    });
            case 'helpLie': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('mc', 'Mc', 1, null, 'pL')},
                    {...keys.getSis('sisFast', 1, 2, null, 'aR'), convoId: 'mistake', textId: node, text: [
                        {text: `What!? That can't be right! I copied it down just as you said, or at least I thought I did`},
                    ]},
                ],
                choice: {options: [
                        <Text text={`"just joking, that was the work rate formula"`}  out={[
                                {type: 'set', path: 'temp.node', value: 'helpLie2'}
                        ]} />,
                    ]}
                });
            case 'help': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                audio: [
                    {id: "f1-sigh"}
                ],
                    img: [
                        {...keys.getSis('mc', 'Mc', 0, null, 'pL')},
                        {...keys.getSis('sisFast', 1, 2, null, 'aR'), convoId: 'mistake', textId: node, text: [
                            {text: `Looking at this equation...`},
                            {latex: demoEq.latex},
                            {text: `I think this is a work rate equation...`, fontSize: '0.65'},
                        ]},
                    ],
                    choice: {options: [
                    <Text text={`"It is"`}  out={[{type: 'set', path: 'temp.node', value: 'help1'}]} />,
                    <Text text={`(lie) "No that's actually the pythagoras equation"`}  
                    out={[{type: 'set', path: 'temp.node', value: 'helpLie'}]} />,
                ]}});
            case 'question': return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                        audio: [
                            {id: "strangeShop", loop: true, volume: 0.3}
                        ],
                        img: [
                            {...keys.getSis('mc', 'Mc', 1, 'iL', 'pL')},
                            {...keys.getSis('sisFast', 1, 3, 'iR', 'aR'), convoId: 'mistake', textId: node, text: [
                                {text: `Theres something I dont understand about these 
                                fractions youre divining the time we'll finish from`},
                            ]},
                        ],
                        choice: {options: [
                                <Text text={`"what don't you understand?"`}  out={[
                                        {type: 'set', path: 'temp.node', value: 'help'}
                                ]} />,
                                <Text text={`"I don't have time for this"`}  out={[
                                    {end: true}
                            ]} />
                            ]}
                    });
                    case 'trash': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                        img: [
                            {...keys.getSis('mc', 'Mc', 1, 'iL', 'pL')},
                            {...keys.getSis('sisFast', 1, 2, 'iR', 'aR'), convoId: 'mistake', textId: node, text: [
                                {text: `Oops I forgot! I'll get right on that...`},
                            ]},
                        ],
                        choice: {options: [
                                <Text text={"next"}  out={[
                                        {type: 'set', path: 'temp.node', value: 'question'}
                                ]} />,
                            ]}
                        });
                    default: return Scene(`dgFastSisterDenominator-${node}`, {settings, viewProps, makeChange,
                        img: [
                            {...keys.getSis('mc', 'Mc', 1, 'iL', 'pL')},
                            {...keys.getSis('sisFast', 1, 3, 'iR', 'aR'), convoId: 'mistake', textId: node, text: [
                                {text: `Hi ${keys.sisMc}, are you okay? it must be difficult living without magic. 
                                Did you need me to help with something?`},
                            ]},
                        ],
                        choice: {options: [
                                <Text text={`"Yeah I need you to take the trash out"`}  out={[
                                        {type: 'set', path: 'temp.node', value: 'trash'}
                                ]} />,
                                <Text text={`"No, I'm fine. Thanks for asking"`}  out={[
                                    {type: 'set', path: 'temp.node', value: 'question'}
                            ]} />
                            ]}
                     });
        }
    }
}

