import React from 'react';
import {get, DELETED} from 'utilities/Merge';
import Text from 'game/Views/TextManager/TextManager';
import Scene from 'game/Views/Scene/Scene.js';
import {log} from 'utilities/Measure';
import {MCQ,Operation, GaEvent} from 'utilities/Create.js';
import {getEqSide, getImg, roundTo, listToEnglish, shuf} from 'utilities/Format.js';
import {operate, EQLR, expand, toLatex, solveFor, evaluate, toNum, 
        least_common_multiple, getVariables} from 'utilities/Game/MathFun';
import { MULT, POW, DIV, DIFF, doOperation} from 'utilities/Game/TreeMath.js';
import {getEmployeeDescription} from './../texts.js';
import {partialEmployeeView} from './../views.js';
import {getFraction} from 'utilities/Game/Latex';
import {getWorkRateEqs} from './../definitions';
import keys from './../keys';
import {dgWin as dialogueWin} from './dialoguesWinLose';
import {dgIntro as dialogueIntro} from './dialoguesPlot';
import {dgGoodEnd, dgBadEnd} from './dialogueEnds';
import {dgEmployeeSection as dialogueEmployeeSection} from './dialoguesPlaces';
import {combinedWorkRate} from './dialogueWorkRatesCombine';
import {dgCombinedWorkRateFormula, 
        dgUnitConversion,  dgSampleSiteSize} from './dialogueWorkRates';
import {dgWorkRateTheory} from './dialogueWorkRateFormula.js';
import {dgWorkRateNeg, dgVarOnLhs, dgGeneric, dgSecondsToHoursMistake, dgTimeRatioMistake,
    dgCombineVarRhs, dgNoVar, dgTwoVars, dgProportions, dgGramsToKilogramsMistake} from './dialoguesMistakes';
import {dgClientStory} from './dialogueClients';
import {dgFastSisterDenominator, dgHeadSister, dgSister2} from './dialogueSisters';

export const getDialogue = (key) => {
    switch(key){
        case 'dgWin': return dialogueWin;
        case 'dgIntro': return dialogueIntro;
        case 'dgGoodEnd': return dgGoodEnd;
        case 'dgBadEnd': return dgBadEnd;
        case 'dgWorkRateTheory': return dgWorkRateTheory;
        case 'dgSampleSiteSize': return dgSampleSiteSize;
        case 'dgEmployeeSection': return dialogueEmployeeSection;
        case 'dgProportions': return dgProportions;
        case 'dgGramsToKilogramsMistake': return dgGramsToKilogramsMistake;
        case 'dgHeadSister': return dgHeadSister;
        case 'dgSister2': return dgSister2;
        case 'dgClientStory': return dgClientStory;
        case 'dgCombinedWorkRate': return combinedWorkRate;
        case 'dgCombinedWorkRateFormula': return dgCombinedWorkRateFormula;
        case 'dgUnitConversion': return dgUnitConversion;
        case 'dgFastSisterDenominator': return dgFastSisterDenominator;

        case 'customerGeneric': return dgGeneric(key);

        case 'dgSecondsToHoursMistake': return dgSecondsToHoursMistake;
        case 'dgTimeRatioMistake': return dgTimeRatioMistake;
        case 'dgCombineVarRhs': return dgCombineVarRhs;
        case 'dgVarOnLhs': return dgVarOnLhs;
        case 'dgNoVar': return dgNoVar;
        case 'dgTwoVars': return dgTwoVars;
        case 'dgCombineVarRhs': return dgCombineVarRhs;
        // case 'dgWorkRateNeg': return dgWorkRateNeg;
         case 'rhsVar': 
         case 'nonXVar': 
         case 'dgWorkRateZero': 
        default: return dgGeneric(key);
    }
}



export const dgEmployeeWorkRate = {
    key: 'dgEmployeeWorkRate',
    pull: (read, write) => {
        let employeeName = get('meta.newEmployee', read, write, 0, true);
        let applicants = get('employees', read, write, null, true);
        let employee =  applicants[employeeName];
   
        //equation options = 
        let equations = [
            {
                eq: `1/${employee[keys.pWorkTotal]}=1/${keys.steadyStellaWorkRate}+1/${keys.eqVarWorker}`,
                latex: `${getFraction(1, roundTo(employee[keys.pWorkTotal], 2))}
                =${getFraction(1, keys.steadyStellaWorkRate)}
                +${getFraction(1, keys.eqVarWorker)}`,
            },
            {
                eq:`1/${keys.eqVarWorker}=1/${keys.steadyStellaWorkRate}+1/${keys.steadyStellaWorkRate*2}`,
                latex: `${getFraction(1, keys.eqVarWorker)}=
                    ${getFraction(1, keys.steadyStellaWorkRate)}
                    +${getFraction(1, keys.steadyStellaWorkRate*2)}`,
            },
            {
                eq: `1/x=1/${keys.steadyStellaWorkRate}+1/y`,
                latex: `${getFraction(1, keys.eqVarWorker)}=
                        ${getFraction(1, keys.steadyStellaWorkRate)}
                        +${getFraction(1, 'y')}`,
            },
           
            {
                eq: `1/${employee[keys.pWorkTotal]}=1/y+1/${keys.eqVarWorker}`,
                latex: `${getFraction(1, roundTo(employee[keys.pWorkTotal], 2))}
                =${getFraction(1, 'y')}
                +${getFraction(1, keys.eqVarWorker)}`,
            }
        ]
        return {employeeName, employee: applicants[employeeName], equations};
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        let equations = get('temp.equations', read, write, null);
        let eqWorkRate = get('temp.eqWorkRate', read, write, null);
        let employee = get('temp.employee', read, write, null);
        let eqFractionsRemoved = get('temp.eqFractionsRemoved', read, write, null);
        let eqResults = get('temp.eqResults', read, write, null);
        let eqSolved = get('temp.solved', read, write, null);
     
        
        switch(node){
            case 'solved':
                let solvedX = get('temp.solved', read, write, null);
                log(`dgEmployeeWorkRate node solved solvedX ${solvedX} round? ${roundTo(solvedX, 1) === 0}`, roundTo(solvedX, 1));
                if(roundTo(solvedX, 1)>0.1){
                    return Scene('dgEmployeeWorkRate-solved',{
                        settings, viewProps, makeChange,
                        text: [
                            `I think ${write.temp.employeeName} works at ${roundTo(solvedX, keys.dp)} hours per job`
                        ],
                        choice: {
                            options: [<Text text="next" out={{end: true}} />]
                        }
                    })
                }else{
                    return Scene('dgEmployeeWorkRate-solved0',{
                        settings, viewProps, makeChange,
                        text: [
                            `Wait...a work rate of ${roundTo(solvedX, keys.dp)} is really, really low...`
                        ],
                        choice: {
                            options: [<Text text="next" out={[
                                {type: 'set', path: 'temp.mistake', 
                                value: {key: solvedX < 0?'dgWorkRateNeg':'dgWorkRateZero', eq: roundTo(solvedX, keys.dp), employee}
                               // value: solvedX < 0?'dgWorkRateNeg':'dgWorkRateZero'
                                }, 
                                {end: true}
                            ]} />]
                        }
                    })
                }
                
            case 'xVal':
                let solvedOptions = get('temp.solvedOptions', read, write, null);
                let solveEq = get('temp.solveEq', read, write, null);
                return Scene('dgEmployeeWorkRate-xVal',{
                    settings, viewProps, makeChange,
                    text: [
                        `What will x resolve to in the equation`,
                        {latex: solveEq.latex}
                    ],
                    
                    img: [
                        {id: 'employee', 
                       // src: getImg(viewProps.meta, keys[`${write.temp.employeeName}-neutral`]), 
                        imageId: keys.getImg(write.temp.employeeName), pose: 'neutral',
                        initial: ['iR'], animate: ['aR']},
                    ],
                    choice: {
                        options: solvedOptions.map((e, i)=> {
                            return <Text text={roundTo(e, keys.dp)} out={[
                                MCQ(keys.acGameMCQ, keys.qSoleWorkRateXSol,  i + '', i === 0?100:0,
                                i === 0?null:[
                                    {text: `Player answered ${roundTo(e, keys.dp)}, the answer was ${roundTo(solvedOptions[0], keys.dp)}`},
                                    {space: true}
                                ]),
                                {type: 'set', path: 'temp.solved', value: e},
                                {type: 'set', path: 'temp.node', value: 'solved'}
                            ]} />
                        })
                    }
                })
            case 'fractionsRemoved':
                let eqCommonDenominator = get('temp.commonDenominator', read, write, null);
                return Scene('dgEmployeeWorkRate-fractionsRemoved',{
                    settings, viewProps, makeChange,
                    text: [
                        `What will the equation, `,
                        {latex: eqWorkRate.latex},
                        `,look like after being multiplied by`,
                        {latex: eqCommonDenominator.latex},
                        `?`
                    ],
                    
                    img: [
                        {id: 'employee', 
                        //src: getImg(viewProps.meta,  keys[`${write.temp.employeeName}-neutral`]), 
                        imageId: keys.getImg(write.temp.employeeName), pose:'neutral',
                        initial:['iR'], animate: ['aR']},
                    ],
                    choice: {
                        onInput: (id, val) => {
                            //solve for the equation
                            let eq = val.eq;
                            //t solveFor = (eq, v)
                            let solved = solveFor(eq, 'x')[0].eq;
                            log(`dgEmployeeWorkRate eq ${val.eq} solved`, solved);
                            let solvedOptions = [solved, solved*10, solved*0.01, solved*-1];
                            log(`dgEmployeeWorkRate eq ${val.eq} solvedOptions`, solvedOptions);
                            makeChange(id, [
                                MCQ(keys.acGameMCQ, keys.qSoleEqMultFrac, 
                                        `${val.i === 0?100:0}`, val.i === 0?100:0, val.i === 0?null:[
                                            {text: `Player needed to identify how the equation `},
                                            {latex: eqWorkRate.latex},
                                            {text: `would look after multiplying it by`},
                                            {latex: eqWorkRate.latex},
                                            {text:`the player selected`},
                                            {latex: val.latex},
                                            {text:`but the answer was`},
                                            {latex: eqResults[0].latex}
                                        ]),
                               // {type: 'set', path: 'temp.solved', value: solved[0]},
                                {type: 'set', path: 'temp.solvedOptions', value: solvedOptions},
                                {type: 'set', path: 'temp.solveEq', value: {eq: val.eq, latex: val.latex}},
                                {type: 'set', path: 'temp.node', value: 'xVal'}
                            ])
                        },
                        options: eqResults.map((e, i) => {
                            return <Text latex={e.latex} eq={e.eq} i={i} />
                        })
                    }
                })
            case 'cd': 
                let cd = employee.workTotal*keys.steadyStellaWorkRate;
                let cdOptions = [cd, cd*12, cd*0.1, cd*-4];
                cdOptions = cdOptions.map(c => {
                    return {eq: c + keys.eqVarWorker, latex: toLatex(c + keys.eqVarWorker)}
                })
                log(`dgEmployeeWorkRate steadyStella ${keys.steadyStellaWorkRate} 
                        workTotal ${employee.workTotal} 
                        cd ${cd} options ${cdOptions} employee`, employee);
                return Scene('dgEmployeeWorkRate-cd',{
                    settings, viewProps, makeChange,
                    text: [
                        `What is the least common multiple that would clear the equations,`,
                        {latex: eqWorkRate.latex},
                        `,fractions out? `,
                    ],

                    img: [
                        {id: 'employee', 
                        //src: getImg(viewProps.meta,  keys[`${write.temp.employeeName}-neutral`]), 
                        imageId: keys.getImg(write.temp.employeeName), pose: 'neutral',
                        initial: ['iR'], animate: ['aR']},
                    ],
                    choice: {
                        onInput:(id, val) => {
                            let cdVal = val.eq;
                            let operation = Operation('multiply', [cdVal]);
                            let results = operate(operation, eqWorkRate.eq, 4, EQLR, true);
                            log(`dgEmployeeWorkRate cd ${cdVal} results`, results);
                            results = results.map(e => {
                                //return e.equation;
                                return expand(e.equation.eq);
                            })
                            log(`dgEmployeeWorkRate post expansion results`, results);
                            makeChange(id, [
                                MCQ(keys.acGameMCQ, keys.qSoleCommonMult, 
                                        `${val.i === 0?100:0}`, val.i === 0?100:0, val.i === 0?null:[
                                    {text: `Player had to select the least common multiple that would cancel out all the fractions in the equation`},
                                    {latex: eqWorkRate.latex},
                                    {text: `the player selected`},
                                    {latex: val.latex},
                                    {text: `but the answer was actually`},
                                    {latex: cdOptions[0].latex}
                                ]),
                                {type: 'set', path: 'temp.eqFractionsRemoved', value: val},
                                {type: 'set', path: 'temp.eqResults', value: results},
                                {type: 'set', path: 'temp.commonDenominator', value: {eq: val.eq, latex: val.latex}},
                                {type: 'set', path: 'temp.node', value: 'fractionsRemoved'},
                            ])
                        },
                        options: cdOptions.map((e, i) => {
                            return <Text i={i} {...e} />
                        })
                }
                })
            case 'pickEq':
                return Scene('dgEmployeeWorkRate-pickEq', {settings, viewProps, makeChange,
                    text: [`According to ${keys.nSt}'s observations, 
                            together they took ${roundTo(employee[keys.pWorkTotal], keys.dp)} hours 
                            to complete 1 unit of work. I know ${keys.nSt} completes 
                            1 unit of work in ${keys.steadyStellaWorkRate} hours.
                            I want to know how fast ${write.temp.employeeName} works. 
                            I'll use ${keys.eqVarWorker} to represent their work rate.
                            Therefore the work rate equation should look like...`],
                                                
                    img: [
                            {id: 'employee', 
                            //src: getImg(viewProps.meta,  keys[`${write.temp.employeeName}-neutral`]), 
                            imageId: keys.getImg(write.temp.employeeName), pose: 'neutral',
                                initial: ['iR'], animate: ['aR']},
                    ],
                    choice: {
                        onInput:(id, v) => {
                            //if contains any variable other than x, mistake
                            try{
                              //  let lhs = getEqSide(v.eq, 0);
                                let lhsVariables =  getVariables(getEqSide(v.eq, 0));
                                let allVariables = getVariables(v.eq).filter(f => f !== keys.eqVarWorker);
                                //variables = variables.filter(f => f !== keys.eqVarWorker);
                               
                                if(allVariables.length > 0){
                                    makeChange(id, [
                                        MCQ(keys.acGameMCQ, keys.qMistSWRCorrEq,  'nonXVar', 0, [
                                            {text: `When selecting an equation to find an individual employee's work rate, the player selected an equation,`},
                                            {latex: v.latex},
                                            {text: `with multiple variables, when an equation with only 1 variable was required.`}
                                        ]),
                                        // MCQ(keys.acGameMCQ, keys.qSoleCommonMult, 
                                        //     'nonXVar', 0),
                                        {type: 'set', path: 'temp.mistake', value:  {key: 'nonXVar', eq: v, employee}},
                                        {end: true},
                                    ])
                                }else if(lhsVariables.length > 0){
                                    makeChange(id, [
                                        MCQ(keys.acGameMCQ, keys.qMistSWRCorrEq,  'varOnLhs', 0, [
                                            {text: `When selecting an equation to find an individual employee's work rate, the player selected an equation,`},
                                            {latex: v.latex},
                                            {text: `with the variable on the left hand side of the equation.`}
                                        ]),
                                        {type: 'set', path: 'temp.mistake', value: {key: 'varOnLhs', eq: v, employee}},
                                        {end: true},
                                    ]);
                                    return;
                                }else{
                                    makeChange(id, [
                                        {type: 'set', path: 'temp.node', value: 'cd'},
                                        {type: 'set', path: 'temp.eqWorkRate', value: v},
                                        MCQ(keys.acGameMCQ, keys.qMistSWRCorrEq, 'correctEq', v.i === 0?100:0),
                                    ])
                                }
                            }catch(e){
                                //throws error if there are no variables, therefore both sides are numbers
                                makeChange(id, [
                                    // MCQ(keys.acGameMCQ, keys.qSoleCommonMult, 
                                    //     'noVar', 0),
                                    MCQ(keys.acGameMCQ, keys.qMistSWRCorrEq,  'noVar', 0, [
                                        {text: `When selecting an equation to find an individual employee's work rate, the player selected an equation,`},
                                        {latex: v.latex},
                                        {text: `with no variable to represent said unknown work rate.`}
                                    ]),
                                    {type: 'set', path: 'temp.mistake', value: {key: 'noVar', eq: v, employee}},
                                    {end: true},
                                ]);
                            }
                        },
                        options: equations.map((e, i) => {
                        return <Text i={i} {...e} />
                    })}});
            default:
                let description = getEmployeeDescription(write.temp.employeeName);
                return Scene('dgEmployeeWorkRate-start', {settings, viewProps, makeChange,
                    text: [`The new applicant is described by their peers as ${description}.
                    I need to find their work rate based on the data from their test cleaning
                    session with ${keys.nSt}.`],
                    
                    img: [
                        {id: 'employee', 
                       // src: getImg(viewProps.meta, keys[`${write.temp.employeeName}-neutral`]), 
                        imageId: keys.getImg(write.temp.employeeName), pose: 'neutral',
                            initial: ['pR'], animate: ['aR']},
                    ],
                    choice: {options: [
                        <Text text={"begin"} out={{type: 'set', path: 'temp.node', value: 'pickEq'}} />,
                        <Text text={"back"} out={{end:true}} />,
                        ]}});
        }
    }
}


export const dgSolveJob = {
    key: 'dgSolveJob',
    pull: (read, write) => {
        log(`dgSolveJob pull read, when retriggered, temp.workers was null for some reason`, read);
        log(`dgSolveJob pull write`, write);
        let employees = get('employees', read, write, null, true);
        let employeesNames = Object.keys(employees)
        let workers = employeesNames.filter(f => {
            log(`dgSolveJob employee name ${f} props ${keys.pPwr} has prop? ${employees[f].hasOwnProperty(keys.pPwr)}`);
            return employees[f].hasOwnProperty(keys.pPwr);
        }).map(w => {
            return employees[w];
        });
        log(`dgSolveJob employeeNames ${employeesNames} workers`, workers);
        return {index: 0, workers};
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        log(`dgSolveJob read dialogue`,read);
        log(`dgSolveJob write`, write);
        let employees = get('temp.workers', read, write, null);
        let assigned = get('temp.assigned', read, write, {});
        let skills = get('temp.skills', read, write, []);
        let eqs = get('temp.eqs', read, write, [], false);
        let jobId = get('meta.jobId', read, write, null, false);
        let job = get('clients.' + jobId, read, write, null, false);
        log(`dgSolveJob id ${jobId} job`, job);
        let eq = get('temp.eq', read, write, null, false);

        log(`dgSolveJob employees`, employees);
        let employeeNames = Object.keys(employees);
        let index = get('temp.index', read, write, 0);
        let remainder = index%(employeeNames.length);
        let employee = employees[employeeNames[remainder]];


        log(`dgSolveJob job id ${jobId}`, job);
        log(`dgSolveJob get eqs`, eqs);
        log(`dgSolveJob assigned`, assigned);
        log(`dgSolveJob skills`, skills);
        log(`dgSolveJob index ${index} remainder ${remainder} 
                employeeName ${employeeNames[remainder]} employeeNames ${employeeNames} employee`, employee);
        switch(node){
            case 'workEnd': 
                let totalTime = get('temp.totalTime', read, write, [], false);
                return Scene('dgSolveJob-workEnd', {settings, viewProps, makeChange,
                    text: [
                        {text: `They should be finished in ${roundTo(totalTime.eq, 2)} hours. 
                        To avoid workers violation fines, they need to work less than ${keys.workMax} hours,
                        and to avoid efficiency problems, they need to work more than ${keys.workMin}.`},
                    ],
                    choice: {options: [
                    <Text text={"This team is good enough"}  out={{end: true}} />,
                    <Text text={"Re-assign workers"}  out={[
                        {type: 'set', path: 'temp', value: {index: 0, workers: get('temp.workers', read, write, null)}},
                        {type: 'set', path: 'temp.node', value: 'start'},
                    ]} />,
                ]
                }});
            case 'solution': 
                let multipliedEq = get('temp.multipliedEq', read, write, [], false);
                let solution = get('temp.solution', read, write, [], false);
                let totalTimeOptions = get('temp.totalTimeOptions', read, write, [], false);
                return Scene('dgSolveJob-solution', {settings, viewProps, makeChange,
                    text: [
                        {text: `The solution to `},
                        {latex: multipliedEq.latex},
                        {text: `is ${solution}. `},
                        {text: `So ${listToEnglish(assigned, (k, v) => {return k})} can get 1 unit of work done in ${solution} hours.`},
                        {text: `How long will it take them to finish ${job.workUnits} units of work?`}
                    ],
                    choice: {options: totalTimeOptions.map((e, i) => {
                        return <Text {...e}  out={[
                            MCQ(keys.acGameMCQ, keys.qTotalTime, i + "", i === 0?100:0, i === 0?null:[
                                {text: `Player had to mentally solve the equation`},
                                {latex: multipliedEq.latex},
                                {text: `to get the answer`},
                                {latex: totalTimeOptions[0].latex},
                                {text: `but they chose `},
                                {latex: e.latex}
                            ]),
                            {type: 'set', path: 'temp.node', value: 'workEnd'},
                            {type: 'set', path: 'temp.totalTime', value: e},
                        ]} />
                    })}});
            case 'multipliedEqs':
                let multiplied = get('temp.multipliedEqs', read, write, [], false);
                let lcm = get('temp.lcm', read, write, null, false);
                return Scene('dgSolveJob-multipliedEqs', {settings, viewProps, makeChange,
                    text: [
                        {text: `Whats the result of multiplying`},
                        {latex: eq.latex},
                        {text: `by`},
                        {latex: lcm.latex},
                        {text: '?'}
                    ],
                    choice: {
                        onInput: (id, v) => {
                           // let selectedEq = v.eq;
                            let i = v.i;
                            let solved = solveFor(v.eq, 'x');
                            let solvedValue = toNum(solved[0].eq);
                            let solvedTotal = roundTo(job.workUnits*solvedValue, keys.dp);
                            let totalTimeOptions = [solvedTotal, solvedTotal*10, solvedTotal*-1, solvedTotal*0.1];
                            totalTimeOptions = totalTimeOptions.map(e => {
                                return {eq: e, latex: toLatex(`${e}`)};
                            })
                            log(`dgSolveJob solvedValue ${solvedValue} solvedTotal ${solvedTotal} solved`, solved)
                            log(`dgSolveJob v.eq `, v.eq)
                            makeChange(id, [
                                MCQ(keys.acGameMCQ, keys.qTotalMultLcm, i + "", i === 0?100:0, i === 0?null:[
                                    {text: `Player tried to multiply `},
                                    {latex: eq.latex},
                                    {text: `by`},
                                    {latex: lcm.latex},
                                    {text: `but they selected`},
                                    {latex: v.latex},
                                    {text: `when the answer was actually`},
                                    {latex: multiplied[0].latex}
                                ]),
                                {type: 'set', path: 'temp.solution', value: roundTo(solved[0].eq, keys.dp)},
                                {type: 'set', path: 'temp.totalTimeOptions', value: totalTimeOptions},
                                {type: 'set', path: 'temp.multipliedEq', value: {eq: v.eq, latex: v.latex}},
                                {type: 'set', path: 'temp.node', value: 'solution'},
                            ])
                        },
                        options: multiplied.map((e, i) => {
                            return <Text {...e} i={i} />
                        })
                    }
                    
                });
            case 'lcm': 
                let lcms = get('temp.lcms', read, write, [], false);
                
                return Scene('dgSolveJob-lcm', {settings, viewProps, makeChange,
                    text: [
                        {latex: eq.latex},
                        {text: `I need to get rid of the fractions. What is the 
                        least common multiple that can remove all the fractions?`},
                    ],
                    choice: {
                        onInput: (id, v) => {
                            let workRate = v.eq;
                            let i = v.i;
                            log(`dgSolveJob multiply lcm ${workRate} eq ${eq}`, eq);
                            let operation = Operation('multiply', [workRate]);
                            let result = operate(operation, eq.eq, 4, EQLR);
                            log(`dgSolveJob multiply lcm result`, result);
                            result = result.map(e => {
                                return e.equation;
                            })
                            makeChange(id, [
                                MCQ(keys.acGameMCQ, keys.qTotalLcm, i + "", i === 0?100:0, i === 0?null:[
                                    {text: `When selected the least common multiple that would cancel all fractions in the equation`},
                                    {latex: eq.latex},
                                    {text: `the player selected`},
                                    {latex: v.latex},
                                    {text: `when the answer was actually`},
                                    {latex: lcms[0].latex}
                                ]),
                                {type: 'set', path: 'temp.multipliedEqs', value: result},
                                {type: 'set', path: 'temp.node', value: 'multipliedEqs'},
                                {type: 'set', path: 'temp.lcm', value: {eq: v.eq, latex: v.latex}},
                            ])
                        },
                        options: lcms.map((e, i) => {
                            return <Text {...e} i={i} />
                        })
                    }
                    
                });
            case 'pickFormula': return Scene('dgSolveJob-pickFormula', {settings, viewProps, makeChange,
                text: [
                    {text: `pick an equation`},
                ],
                choice: {
                    onInput: (id, val) => {
                        let i = val.i;
                        let actions = [];
                        //if i === 0, correct
                        log(`dgSolveJob selected val`, val);
                        if(i === 0){
                            actions.push(MCQ(keys.acGameMCQ, keys.qTotalEq, 'correct', 100));
                            let hasDecimal = false;
                            val.workRates.map(e => {
                                if(!hasDecimal && `${e}`.indexOf('.')>-1){
                                    hasDecimal = true;
                                }
                            })
                            let workRate;
                            if(hasDecimal){
                                workRate = roundTo(
                                    val.workRates.reduce((a, e, i) => {
                                        return a*e;
                                    }, 1),
                                    2
                                );
                                let max = 0;
                                let counter = 0;
                                val.workRates.map(w => {
                                    if(max < w) max = w;
                                })
                                if(workRate < max){
                                 
                                    while(counter < 100 && (workRate*counter < max)){
                                        counter++;
                                    }
                                    if(counter > 0) workRate = workRate*counter;
                                }
                                log(`dgSolveJob cd calculated by multiplication, cd: ${workRate} max ${max} counter ${counter} rates`, val.workRates)
                            }else{
                                workRate = val.workRates.reduce(least_common_multiple);
                                log(`dgSolveJob cd calculated by least_common_multiple, cd: ${workRate} rates`, val.workRates)
                            }
                            let workRateOptions = [
                                `${workRate}*x`,
                                `${workRate*-1}*x`,
                                `${workRate}*y`,
                                `${workRate*-1}*y`
                            ]
                            workRateOptions  = workRateOptions.map(e => {
                                return {eq: e, latex: toLatex(e)};
                            })
                            log(`dgSolveJob workRate ${workRate} options`, workRateOptions);
                            actions.push({type: 'set', path: 'temp.lcms', value: workRateOptions});
                            actions.push({type: 'set', path: 'temp.node', value: 'lcm'});
                            actions.push({type: 'set', path: 'temp.eq', value: {eq: val.eq, latex: val.eq}});
                        }else{
                            //getVariables = (expression)
                            let rhs = getEqSide(val.eq, 1);
                            if(getVariables(val.eq).length === 0){
                                actions.push(MCQ(keys.acGameMCQ, keys.qTotalEq, 'noVar', 0, [
                                    {text: `The player had to pick the work rate formula that would allow them 
                                    to solve for the employee's combined work rate. The answer was`},
                                    {latex: eqs[0].latex},
                                    {text: `but the player selected the equation`},
                                    {latex: val.latex},
                                    {text: `With no variables to solve for the unknown total work rate.`}
                                ]));
                                actions.push({type: 'set', path: 'temp.mistake', 
                                value: {key: 'noVar', employee}
                                //value: 'noVar'
                                });
                                actions.push({end:true});
                            }else if(getVariables(rhs).length > 0){
                                actions.push(MCQ(keys.acGameMCQ, keys.qTotalEq, 'rhsVar', 0, [
                                    {text: `The player had to pick the work rate formula that would allow them 
                                    to solve for the employee's combined work rate. The answer was`},
                                    {latex: eqs[0].latex},
                                    {text: `but the player selected the equation`},
                                    {latex: val.latex},
                                    {text: `with variables on the right hand side of the equation, 
                                    indicating they misunderstood what they were solving for.`}
                                ]));
                                actions.push({type: 'set', path: 'temp.mistake',
                                    value: {key: 'rhsVar', employee}
                                 //value: 'rhsVar'
                                });
                                actions.push({end:true});
                            }
                        }
                        makeChange(id, actions);
                        //else if eq contains on rhs, ask how that solves for total time
                        //else if doesnt contain x at all, ask how it solves for anything
                        //else if contains the wrong numbers, ask how it solves for the current employees
                    },
                    options: eqs.map((eq, i) => {
                    return <Text {...eq} i={i} />
                })}
            });
            case 'noneAssigned': return Scene('dgSolveJob-noneAssigned', {settings, viewProps, makeChange,
                    text: [
                        {text: `You need to assign employees before a job can be done`},
                    ],
                    choice: {
                        options: [
                            <Text text="okay"  out={[
                                {type: 'set', path: 'temp.node', value: 'start'}
                            ]} />
                        ]
                    }
                    
                });
            case 'noSkills': return Scene('dgSolveJob-noSkills', {settings, viewProps, makeChange,
                    text: [
                        {text: `There is a job skill mismatch. The job requires specializations in
                        ${listToEnglish(job.specs)}, the workers you currently have assigned have skills
                        in ${listToEnglish(skills)}`},
                    ],
                    choice: {
                        options: [
                            <Text text="okay"  out={[
                                {type: 'set', path: 'temp.node', value: 'start'}
                            ]} />
                        ]
                    }
                    
                });
            default: 
                let rand = `${Math.random()}`;
                // let isSomeoneAssigned = false;
                // Object.keys(assigned).map(e => {
                //     if(assigned[e]) isSomeoneAssigned = true;
                // })
                let employeeText = partialEmployeeView(employee, viewProps);
                return Scene('dgSolveJob', {settings, viewProps, makeChange,
                textClassName: 'text-left',
              //  textId: `assigned${Object.keys(assigned).length}`,
                text: [
                    {text: `Job skill requirements: ${listToEnglish(job.specs)}`, tag: 'h4', className:'text-center'},
                    {text: `Current skills: ${listToEnglish(skills)}`, tag: 'h4', className:'text-center'},
                    {text: 'Assign some employees to the task', className: 'text-center block'},
                    {divide: true},
                    ...employeeText.text,
                    //...eText
                ],
                img: employeeText.img,
                // extras: [
                //     <Text key={`assigned${rand}`} id={`assigned${rand}`} text={eText} 
                //         settings={settings} viewProps={viewProps} mergeInput={makeChange} />
                // ],
                choice: {options: [
                    <Text text={`back`} out={{end: true}}/>,
                    <Text text={`next ${remainder+1}/${employeeNames.length}`} out={[
                        {type: 'set', path: 'temp.index', value: index + 1}
                    ]}/>,
                    <Text key={`assigned${rand}`} id={`assigned${rand}`} text={assigned[employee.name]?`remove`:`assign`}
                        employee={employee}
                        assigned={assigned[employee.name]}
                        onInput={(id, v) => {
                            let skills = {};
                            let nAss = {...assigned};
                            if(v.assigned){
                                delete nAss[employee.name];
                            }else{
                                nAss[employee.name] = true;
                            }
                            employees.map(e => {
                                if(nAss[e.name]){
                                    e.specs.map(s => {
                                        skills[s] = true;
                                    })
                                }
                            })
                            log(`dgSolveJob current skills `, skills)
                            log(`dgSolveJob current assigned employee.name ${employee.name} assigned? ${v.assigned}`, assigned)
                            makeChange(id, [
                                {type: 'set', path:'temp.assigned', value: nAss},
                                {type: 'set', path:'temp.skills', value: Object.keys(skills)}
                            ])
                        }}
                        //out={{type: 'set', path:'temp.assigned.' + employee.name, value: !assigned[employee.name]?true:false}} 
                        />,
                <Text text={"start job"} // disabled={!isSomeoneAssigned} 
                    onInput={(id, val) => {
                        log(`dgSolveJob id ${id} val`, val);
                        //check skills for every job
                        if(skills.length === 0){
                            makeChange(id, [
                                {type: 'set', path: 'temp.node', value: 'noneAssigned'},
                            ])
                            return;
                        }
                        let valid = true;
                        log(`dgSolveJob validating job specs`, job.specs)
                        job.specs.map(e => {
                            
                            if(skills.indexOf(e) === -1){
                                valid = false;
                            }
                            log(`dgSolveJob validating job spec ${e} index in skills ${skills.indexOf(e)} valid ${valid} skills`, skills)
                        })
                        if(!valid){
                            makeChange(id, [
                                {type: 'set', path: 'temp.node', value: 'noSkills'},
                            ])
                            return;
                        }
                        let assignedEmployees = employees.filter(f =>  assigned[f.name]);
                        log(`dgSolveJob start job assignedEmployees`, assignedEmployees);
                        let eqs = [], nums = [], numerator = 1; //can substitute whatever the workload is in
                        for(let j = 0; j<4; j++){
                            eqs.push(
                                {
                                workRates: assignedEmployees.map(e =>{
                                    return e[keys.pPwr];
                                }),
                                eq: `${numerator}/${j%2===0?'x':'10'}=${assignedEmployees.map((e, i) => {
                                    return `${i>0?'+':''}${numerator}/
                                    ${j-1===i?'x':roundTo(e[keys.pPwr], keys.dp)}`
                                }).join('')}`,
                                latex: `${getFraction(numerator, j%2===0?'x':'10')}=${assignedEmployees.map((e, i) => {
                                    return `${i>0?' + ':''}${getFraction(numerator, 
                                        j-1===i?'x':roundTo(e[keys.pPwr], keys.dp))}`
                                }).join(' ')}`
                            })
                        };
                        log(`dgSolveJob eqs latexed`, eqs);
                        makeChange(id, [
                            {type: 'set', path: 'temp.node', value: 'pickFormula'},
                            {type: 'set', path: 'temp.eqs', value: eqs},
                            {type: 'set', path: 'temp.skills', value: skills},
                        ])
                    }} 
                />]}});
        }
    }
}










/* MISTAKE DIALOGUES */
export const getMistakeDialogue = (mistake) => {
    //sort to alphabetical, join to key, check flags
    log(`mistake getMistakeDialogue ${mistake}`, mistake)
    switch(mistake){
        case 'varOnLhs': return dgVarOnLhs;
       // case 'dgWorkRateNeg': return dgWorkRateNeg;
        case 'rhsVar':
        case 'noVar': 
        case 'nonXVar': 
        case 'dgWorkRateZero': 
        default:
            return dgGeneric(mistake);
    }
}




/* WORKER DIALOGUES */
export const getWorkerDialogue = (assigned, flags) => {
    //sort to alphabetical, join to key, check flags
    log(`getWorkerDialogue flags`, flags);
    log(`getWorkerDialogue assigned`, assigned);
    let sorted = assigned.sort();
    log(`getWorkerDialogue sorted`, sorted);
    let key = sorted.join('-');
    log(`getWorkerDialogue key`, key);
    if(!flags[key]){
        switch(key){
            case `${keys.nJo}-${keys.nSt}`: return nStnJo;
            case `${keys.nJo}-${keys.nNe}-${keys.nSt}`: return nJonNenSt;
            case `${keys.nNe}-${keys.nRh}-${keys.nSt}`: return nJonRhnSt;
            case `${keys.nRh}-${keys.nSh}`: return nRhnSh;
            case `${keys.nSt}`: return nSt;
        }
    }
}
