

import React from 'react';
import {get, DELETED} from 'utilities/Merge';
import Text from 'game/Views/TextManager/TextManager';
import Scene from 'game/Views/Scene/Scene.js';
import {log} from 'utilities/Measure';
import {MCQ,Operation, GaEvent } from 'utilities/Create.js';
import {getEqSide, getImg, roundTo, listToEnglish, shuf} from 'utilities/Format.js';
import {operate, EQLR, expand, toLatex, solveFor, evaluate, toNum, 
        least_common_multiple, getVariables} from 'utilities/Game/MathFun';
import keys from './../keys';



export const dgIntro = {
    key: 'dgIntro',
    pull: (read, write) => {
        let workRateFormulas = [
            `1/totalWorkTime=1/workRateA + 1/workRateB`,
            `1/workRateA= 1/totalWorkTime + 1/workRateB`,
            `1/totalWorkTime=1/workRateA - 1/workRateB`,
            `1/totalWorkTime=1/workRateA*1/workRateB`,
        ];
        workRateFormulas = workRateFormulas.map(e => {
            return {eq: e, latex: toLatex(e)}
        })
        return {workRateFormulas}
    },
    dialogue: (id, node, read, write, makeChange, settings, viewProps) => {
        switch(node){
            case 'excellent': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL')},
                    {...keys.getSis('stella', 0, 1, null, 'aR'),
                    text: `Let's give it a shot!`, textId: node },
                ],
                choice: {options: [  <Text text={"next"} out={{end: true}} /> ] }
            });
            case 'warning': 
            let nReviews = get('meta.numReviews', read, write, null);
            return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 1, null, 'aL'),
                    text: `But I won't work on an unprofessional venture. If, after say...${nReviews} reviews, 
                    we don't have a review score higher than ${keys.reviewThreshold}, I'm out.`, 
                    textId: node},
                    {...keys.getSis('stella', 0, 2, null, 'aR') },
                ],
                choice: {options: [
                    <Text text={"next"} out={{type: 'set', path: 'temp.node', value: 'excellent'}} />,
                ]
            }});
            case 'alright': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL'),
                    text: `Alright, I'll let the clients know how long it'll take you girls to...'eat'... the damage.`, 
                    textId: node},
                    {...keys.getSis('stella', 0, 1, null, 'aR') },
                ],
                choice: {options: [
                    <Text text={"next"} out={{type: 'set', path: 'temp.node', value: 'warning'}} />,
                ]
            }});
            case 'whatNumbers2': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 1, null, 'aL')},
                    {...keys.getSis('stella', 0, 0, null, 'aR'),
                    text: `They care about precision, not speed. You have a way of knowing precisely when something will occur.`,
                    textId: node },
                ],
                choice: {options: [
                    <Text text={"I do"} out={{type: 'set', path: 'temp.node', value: 'cleansingNumbers'}} />
                ]
            }});
            case 'whatNumbers': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL')},
                    {...keys.getSis('stella', 0, 0, null, 'aR'),
                    text: `The clientelle are large businesses. They need their schedules to go as planned, 
                    undisrupted by their base of operations being cleansed too early or too late.`,
                     textId: node },
                ],
                choice: {options: [
                    <Text text={"I do"} out={{type: 'set', path: 'temp.node', value: 'whatNumbers2'}} />
                ]
            }});
            case 'whatToxic4': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL')},
                    {...keys.getSis('stella', 0, 1, null, 'aR'),
                    text: `Oh yes~ though ghosts aren't my favorite.`, 
                    textId: node },
                ],
                choice: {options: [
                    <Text text={"next"} out={{type: 'set', path: 'temp.node', value: 'cleansingNumbers'}} />,
                ]
            }});
            case 'whatToxic3': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 3, null, 'aL'),
                    text: `I know that radioactivity and mold are tasty to you, but can you really eat ghosts?`, 
                    textId: node},
                    {...keys.getSis('stella', 0, 0, null, 'aR') },
                ],
                choice: {options: [
                    <Text text={"next"} out={{type: 'set', path: 'temp.node', value: 'whatToxic4'}} />,
                ]
            }});
            case 'whatToxic2': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL')},
                    {...keys.getSis('stella', 0, 1, null, 'aR'),
                    text: `We'll be doing the community a great service, and get something good to eat at last`, 
                    textId: node },
                ],
                choice: {options: [
                    <Text text={"next"} out={{type: 'set', path: 'temp.node', value: 'whatToxic3'}} />,
                ]
            }});
            case 'whatToxic': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL')},
                    {...keys.getSis('stella', 0, 2, null, 'aR'),
                    text: `The worst kind. Radioactive, moldy disasters. Possibly even paranormal ones.`,
                    textId: node},
                ],
                choice: {options: [
                    <Text text={"Good, we'll be giving something back to the community, at last"} out={{type: 'set', path: 'temp.node', value: 'cleansingNumbers'}} />,
                    <Text text={"Why do you want to do it?"} 
                        out={{type: 'set', path: 'temp.node', value: 'whatToxic2'}} />
                ]
            }});
            case 'cleansingNumbers': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL')},
                    {...keys.getSis('stella', 0, 0, null, 'aR'),
                    text: `Between my cleansing experience and your numbers, we could do well.`, 
                    textId: node},
                ],
                choice: {options: [
                    <Text text={'"What do you need my numbers for?"'} out={{type: 'set', path: 'temp.node', value: 'whatNumbers'}} />,
                    <Text text={'"What kind of toxic filth?"'} out={{type: 'set', path: 'temp.node', value: 'whatToxic'}} />,
                    <Text text={`"Alright, I'll get your answers for you"`} out={{type: 'set', path: 'temp.node', value: 'alright'}} />,
                ]
            }});
            case 'begging2': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL')},
                    {...keys.getSis('stella', 0, 0, null, 'aR'),
                    text: `Our cleansing competition is pitifal or non existant.`,  textId: node},
                ],
                choice: {options: [
                    <Text text={"next"} out={{type: 'set', path: 'temp.node', value: 'cleansingNumbers'}} />
                ]
            }});
            case 'begging': return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL')},
                    {...keys.getSis('stella', 0, 2, null, 'aR'),
                    text: `I thought...well, they're not the only companies with sickening messes to clean.`, 
                    textId: node },
                ],
                choice: {options: [
                    <Text text={"next"} out={{type: 'set', path: 'temp.node', value: 'begging2'}} />
                ]
            }});
            case 'start2':return Scene(`${id}-${node}`, {settings, viewProps, makeChange,
                 img: [
                    {...keys.getSis('kara', 'Mc', 0, null, 'aL')},
                     {...keys.getSis('stella', 0, 1, null, 'aR'), 
                     text: [  {text: `The board of directors is practically begging.`} ], 
                     textId: node},
                 ],
                 choice: {options: [
                     <Text text={"next"} out={{type: 'set', path: 'temp.node', value: 'begging'}} />
                 ]
             }});
  
            default: 
            return Scene('dgIntro-start', {settings, viewProps, makeChange,
                audio: [
                    {id: keys.mainTheme, loop: true},
                    {id: "f1-laugh", volume: 0.1}
                ],
                img: [
                    {...keys.getSis('kara', 'Mc', 0, 'iL', 'aL')},
                    {...keys.getSis('stella', 0, 1, 'iR', 'aR'),
                    textId: 'ts1', text: [
                        {text: `They can't find anyone who can clean up that radioactive spill.`}
                    ]}, 
                ],
                choice: {options: [
                    <Text text={"next"} out={{type: 'set', path: 'temp.node', value: 'start2'}} />
                    //<Text text={"next"} out={{end:true}} />
                ]
            }});
        }
    }
}